<template>
    <transition name="bounce"  @after-enter="afterEnter">
        <div v-show="pageStatus.joinAnimation" class="ball-container">
            <img :src="selectedSku.pictureList[0].enlargedURL" alt="" />
        </div>
    </transition>
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    name: "moveball",
    computed:{
      ...mapState({
        'selectedSku': state => state.detailState.data.product.selectedSku,
        'pageStatus': state => state.detailState.pageStatus
      })
    },
    methods:{
      afterEnter:function(el){
        this.pageStatus.joinAnimation = false;
      }
    }
  }
</script>

<style lang="scss" scoped>
.ball-container{
    position: fixed;
    left: 0;
    top: 0;
    width:100%;
    height:3.75rem;
    // line-height: 3.75rem;
    vertical-align: middle;
    text-align: center;
    border-radius: 50%;
    background-color: #CCD4D8;
    z-index: 104;
    opacity: 0;
    overflow: hidden;
    transform-origin: left top;
    img{
      width: 100%;
      height:100%;
    }
}
.bounce-enter-active {
  animation: bounce-in .8s ease-in;
}
// .bounce-leave-active {
//   animation: bounce-in .5s reverse;
// }
@keyframes bounce-in {
  0% {
    opacity: 1;
  }
  50% {
     transform: scale(.1);
  }
  90%{

    transform: scale(.1);
    left: .7rem;
    top: 93%;
    opacity: 1;
  }
  100% {
    transform: scale(.1);
    left: .7rem;
    top: 93%;
    opacity: 0;
  }
}
</style>



