<template>
  <div class="productParameters">
    <headerNav @back="backToLastPage" :title="tabName" :shareShow="false" />
    <div class="product_describe" style="margin-top:82px;">
      <div ref="htmlBox" class="htmlBox" v-html="getDetail"></div>
      <!-- <iframe :srcdoc="getDetail" ref="htmlBox"  frameborder="0" width="100%" height="90%"   ></iframe> -->
    </div>
  </div>
</template>

<script>
  import {supreme,baseMixin,scrollBehaviorMixin,type} from '@/supreme-core'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import headerNav from '@/components/header-nav.vue'
  export default {
    name: "productParameters",
    mixins: [baseMixin],
    data(){
      return{
        key:'description'
      }
    },
    created(){
      var self=this;
      if(util.isInWebview()){
        hybrid.hideLoading();
      }
    },
    components:{
      headerNav
    },
    computed: {
      newVersion(){
        return util.newVersion();
      },
      tabName(){
        var tabID = supreme.getQueryString("tabid");
        var description ,result;
        var pageData = supreme.localCache.get("description");
        if(pageData){
          for (var desc of  pageData) {
            if (desc.tabId == tabID) {
              description = desc;
              break;
            }
          }
        }else{
          return result;
        }

        try{
          result  =  decodeURIComponent(description.tabName)
        }catch (ex){
          result  = description.tabName
        }
        return result ;
      },
      getDetail() {
        var tabID = supreme.getQueryString("tabid");
        var description ,result;
        var pageData = supreme.localCache.get("description");
        if(pageData){
          for (var desc of pageData) {
            if (desc.tabId == tabID) {
              description = desc;
              break;
            }
          }
        }else{
          return result;
        }

        try{
          result  =  decodeURIComponent(description.detail)
        }catch (ex){
          result  = description.detail
        }
        return result ;
      }
    },
    mounted(){
      setTimeout(e=>{
        // 测试文描图片显示
        this.addEventImages();
      },1000);
    },
    beforeDestroy(){
      // 测试文描图片显示
      this.removeEventImages();
      supreme.sessionCache.delete(this.key);
    },
    methods:{
      ////如果应用是应hybrid 形式回退的名字需要是固定backToLastPage
      backToLastPage(){
        //this.$router.back();
        if(util.isInWebview()){
          hybrid.close();
        }else{
          this.back();
        }
      },
      // 测试文描图片显示
      addEventImages:function(){
        let doms = this.$refs.htmlBox;
        doms.addEventListener('click', this.showImages, false);
      },
      removeEventImages:function(){
        let doms = this.$refs.htmlBox;
        doms.removeEventListener('click', this.showImages, false);
      },
      showImages(dom){
        if(dom.target.nodeName=='IMG'){
          hybrid.showImagesPreview({
            "images":[dom.target.src],
            "index":0
          })
        }else{
          console.log('点击的不是图片')
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .productParameters{
    width: 100%;
    height: 100%;
    overflow:hidden;
    background-color: #ffffff;
  }
  .product_describe{
    width: 100%;
    height: 90%;
    padding: 0 20px;
    overflow-x: scroll;
    h3{
      margin-top: 92px;
      font-size: 30px;
      color: #040B29;
    }
  }
  .htmlBox{
    font-size: 14px;
    background-color: #fff;
  }
</style>



