<template>
  <!-- IphoneX Bar(兼容iphone 刘海屏) -->
  <div :style="{height:isIphoneX?'34px':'0'}"></div>
</template>
<script>
  import util from '@/util/util'
  export default {
    name: "iphonex-bar",
    props: {
      type:{
        type: String,
        default:'top'
      }
    },
    computed:{
      isIphoneX(){
        return util.isIphoneX();
      }
    }
  }
</script>


