<template>
  <div class="errorPage">
    <headerNav @back="backToLastPage" :shareShow="false"  />
    <span @click="backRefresh" class="error_icon"></span>
    <p class="error_text">你要找的页面不见啦！</p>
    <!-- <p class="error_text red">{{message}}</p>
    <p class="error_text red">{{resultDesc}}</p> -->
  </div>
</template>

<script>
  import {supreme,baseMixin} from '@/supreme-core'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import headerNav from '@/components/header-nav.vue'
  export default {
    name: "errorPage",
    mixins: [baseMixin],
    computed: {
      message(){
        return this.getQueryString('message')
      },
      resultDesc(){
        return this.getQueryString('resultDesc')
      }
    },
    components:{
      headerNav
    },
    created(){
      if(util.isInWebview()){
        hybrid.pageLoadFinish();
      }
    },
    methods:{
      backToLastPage(){
        if(util.isInWebview()){
          // 页面返回按钮埋点
          hybrid.buriedPointClick({
            eventKey:'MallItemPage_Back_Click',
            currentPage:'MallItemPage_',
            NextPage:'',
            params:{
              spu_code:supreme.getQueryString("spuCode")
            }
          })
          // 页面离开埋点
          hybrid.trackEndPage();
          // 关闭当前页面
          hybrid.close();
        }else{
          this.back();
        }
      },
      backRefresh(){
        //  this.back();
        //  window.location.reload(true);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .errorPage{
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    // padding-top: 1.58rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    .error_icon{
      margin-top: 168px;
      width: 80px;
      height: 80px;
      background: url('../../assets/svg/error_page_bg.svg') center no-repeat;
    }
    .error_text{
      margin-top: 10px;
      font-size:14px;
      color: #DDDDDD;
     
    }
    .red{
      color: #ff0033;
    }
  }
</style>



