<template>
    <div>
        <div v-show="isShow && isWebview" class="not_webview">
            <div class="not_webview_left">
                <img class="logo" src="../assets/images/logo2.png" alt="">
                <div class="dec_text">
                    <p>蔚来</p>
                    <span>创造愉悦生活方式</span>
                </div>
            </div>
            <div class="not_webview_right">
                <div class="download" @click="downloadBtn">
                    <span>App内打开</span>
                </div>
                <span @click="close" class="close"></span>
            </div>
        </div>
         <div v-if="pageStatus.isToastShow" class="not_webview_toast">
            <ul>
                <li>1. 点击右上角</li>
                <li>2. 用浏览器打开</li>
                <li>3. 进入APP</li>
            </ul>
            <span class="hint_arrow"></span>
            <span @click="closeToast" class="close_button"></span>
        </div>
    </div>
</template>

<script>
import util from "@/util/util";
import { supreme } from "@/supreme-core";
import {mapState, mapMutations} from 'vuex'
export default {
  name: "notWebView",
  data() {
    return {
      isShow: true,
    };
  },
  props: {
    pageType: {
      type: String,
      default: "detail"
    }
  },
  computed: {
    ...mapState({
      'pageStatus':state => state.detailState.pageStatus     
    }),
    isWebview() {
      if (util.isInWebview()) {
        return false;
      } else {
        return true;
      }
    },
    getAndroidUrl(){
      var isiOS = navigator.userAgent.match('iPad') || navigator.userAgent.match('iPhone') || navigator.userAgent.match('iPod'),
          isAndroid = navigator.userAgent.match('Android');
      if(isiOS){
        return 'nio://mall/item?version=2&id=';
      }else{
        return `nio://thirdapp?redirect=${encodeURIComponent('nio://mall/item?version=2&id=')}`;
      }
    },
    gotoNIOApp() {
      if (this.$route.path == "/detail") {
        return `${this.getAndroidUrl}${supreme.getQueryString("spuCode")}`;
      } else if (this.$route.path == "/article") {
        return `nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`;
      } else if (this.$route.path == "/series") {
        return `nio://mall/series?pageCode=${supreme.getQueryString("pageCode")}`;
      }else{
        return `${this.getAndroidUrl}${supreme.getQueryString("spuCode")}`;
      }
    }
  },
  methods: {
    ...mapMutations({
      browserOpenNIO:"detailState/browserOpenNIO"
    }),
    close() {
      this.isShow = false;
    },
    closeToast() {
      this.pageStatus.isToastShow = false;
    },
    downloadBtn() {
      console.log(this.gotoNIOApp)
      this.browserOpenNIO(this.gotoNIOApp);
    }
  }
};
</script>

<style lang="scss" scoped>
.not_webview {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.56rem;
  padding: 0 0.2rem;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 111;
}
.not_webview_left {
  display: flex;
  align-items: center;
  .logo {
    width: 0.35rem;
    height: 0.35rem;
  }
  .dec_text {
    display: flex;
    flex-direction: column;
    margin-left: 0.22rem;
    p {
      font-size: 0.16rem;
      color: #ffffff;
    }
    span {
      font-size: 0.12rem;
      color: #999999;
    }
  }
}
.not_webview_right {
  display: flex;
  align-items: center;
  .download {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 0.76rem;
    height: 0.3rem;
    background-color: #09bcc7;
    border-radius: 2px;
    font-size: 0.14rem;
    color: #ffffff;
    line-height: 0.3rem;
  }
  .close {
    margin-left: 0.18rem;
    width: 0.16rem;
    height: 0.16rem;
    background: url("../assets/images/icon_close2.png") center no-repeat;
    background-size: 0.16rem;
  }
}
.not_webview_toast {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(4, 11, 41, 0.6);
  z-index: 1001;
  .hint_arrow {
    width: 89px;
    height: 85px;
    position: absolute;
    right: 45px;
    top: 19px;
    background: url("../assets/images/icon_hint_arrow.png") center no-repeat;
  }
  .close_button {
    width: 39px;
    height: 39px;
    position: absolute;
    right: 168px;
    bottom: 120px;
    background: url("../assets/images/icon_close_button.png") center no-repeat;
  }
  ul {
    margin: 125px auto 0;
    li {
      font-size: 16px;
      color: #ffffff;
      padding-left: 1.3rem;
      margin-bottom: 0.1rem;
    }
  }
}
</style>



