/**
 * @description
 * */
import api from './api';
import CommonModel from './CommonModel'
import LoginModel from './LoginModel'
import TokenModel from './TokenModel'
import util from '@/util/util'
const  envInfo= util.getEnvInfo();

const createTokenModel = new TokenModel({url:envInfo.tokenBaseUrl+"createtoken"})
const ssoLoginModel = new LoginModel({url:envInfo.tokenBaseUrl+api.ssoLogin});
const doLoginModel=new LoginModel({api:api.login});
const wxCreateTokenModel = new CommonModel({api:api.wx_create_token});
const creditShareModel = new CommonModel({api:api.creditShare});
const detailBySaModel = new CommonModel({api:api.detailbysa});
const addCartBySaModel =new CommonModel({api:api.addCartBySa});
const detailCommentLikedModel =new CommonModel({api:api.detailCommentLiked,noLoading:true});
const cartGetcartCountModel =new CommonModel({api:api.cartGetcartCount,noLoading:true});
const userCreditsModel =new CommonModel({api:api.userCredits,noLoading:true});
const detailCommentListModel =new CommonModel({api:api.detailCommentList,noLoading:true});
const FollowModel = new CommonModel({api:api.follow});
const ArticleModel = new CommonModel({api:api.article});
const reportModel = new CommonModel({api:api.uploadReadingReport});
const getCommentsModel = new CommonModel({api:api.getcomments});
const addCommentModel = new CommonModel({api:api.addcomment});
const addReplytoModel = new CommonModel({api:api.addreplyto});
const articleLikedModel = new CommonModel({api:api.articleliked,noLoading:true});
const commentLikedModel = new CommonModel({api:api.commentliked,noLoading:true});
const deleteCommentModel = new CommonModel({api:api.deletecomment});
const reportCommentModel = new CommonModel({api:api.reportcomment});
const seriesModel = new CommonModel({api:api.series});

export {
    //////////////////任务模块///////////////
    createTokenModel,
    ssoLoginModel,
    wxCreateTokenModel,
    doLoginModel,
    creditShareModel,
    FollowModel,
    detailBySaModel,
    reportModel,
    addCartBySaModel,
    detailCommentLikedModel,
    cartGetcartCountModel,
    userCreditsModel,
    detailCommentListModel,
    ArticleModel,//拉详情
    getCommentsModel,//拉评论
    addReplytoModel,//回复评论
    addCommentModel,//发评论
    articleLikedModel,//文章点赞
    commentLikedModel,//文章评论点赞
    deleteCommentModel,//删除评论
    reportCommentModel,//举报评论
    seriesModel
};



