import axios from 'axios'
//import env from '@/util/env.js'
//import config from '@/config/index'
//var  envInfo= config.envInfo;
import qs from 'qs';
import supreme from '../supreme'
/**
 * @export BaseModel
 * @class BaseModel
 * @desc model 的基类，用来继承
 * @author lei.you
 * @date 2018/2/8
 * */
class BaseModel{
  constructor(config={}) {

    var CancelToken = axios.CancelToken;
    var cancel ;
    config.cancelToke=new CancelToken(function(c){
      cancel=c;
    });
    //config.baseURL=config.baseURL|| envInfo.baseUrl;
    config.timeout=config.timeout||5000;
    var xhttp = axios.create(config);
    this.xhttp=xhttp;
    this._procRequest();
    this._procResponse()

    this.cancel=cancel;
    this.config=config;
  }
  _procRequest(){
    this.xhttp.interceptors.request.use((config) => {
      if(!config.noLoading){
        supreme.indicator.open(config.loadingTxt||"加载中....");
      }
      if (config.method === 'post') {
        config.data = qs.stringify(config.data)

      }
      return config;
    }, (error) => {

      return Promise.reject(error.message)
    })
  }
  _procResponse(){
    this.xhttp.interceptors.response.use((res) => {
      supreme.indicator.close();
      return res
    }, (error) => {
      supreme.indicator.close();
      return Promise.reject(error.message)
    })
  }
  post(param,config){
    /**
     *  扩展config 参数
     * */
    supreme.$.extend(config,this.config);
    return this.xhttp.post(this.config.url,param,config);
  }
  get(param){
    if(param.params){
      params:param.params
      return this.xhttp.get(param);
    }
    var data = {
      params:param
    }
    return this.xhttp.get(data);
  }
  delete(){

  }
  head(){

  }
  patch(){

  }
  exec(param){
   return this.post(param);
  }
    /**
     * @desc 执行数据请求
     * @param {object}  参数
     *
     * */
  execute(param){
    return this.exec(param);
  }
  cancel(){
    this.cancel();
  }
    /**
     * @desc 生成baseModel 实例
     * @param {object} config 配置文件
     * @return {baseModel} baseModel 实例
     * */
  static getInstance(config){
    var model=new BaseModel(config);
    model.config=config;
    return model;
  }
}
export default BaseModel;



