import {BaseStore} from '@/supreme-core';

let state = {
  pageStatus:{
   
  },
  data: {
   
  }
}

let mutations = {
  //初始化数据
  setSeriesData(state, data){
    state.data = data
  }
}



var store= BaseStore.getInstance({
  state: state,
  mutations: mutations,
});
export default {
  namespaced: true,
  state: state,
  mutations: mutations,
}



