<template>
  <div class="goods-single-row"> 
    <SeriesGoods v-for="(dataItem,index) in getGoodsData" :key="index" :goods-data="dataItem" />
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  import SeriesGoods from './SeriesGoods.vue'
  export default {
    name: "GoodsSingle",
    components:{
      SeriesGoods
    },
    props:['componentKey'],
    computed:{
      ...mapState({
        'seriesState': state => state.seriesState.data,
      }),
      getGoodsData(){
        let goodsData = [];
        this.seriesState.seriesList.forEach(item => {
          if(item.type == 'bigItem'&&item.sortKey==this.componentKey){
            goodsData = [item.data];
          }
        });
        return goodsData;
      }
    }
  }
</script>

<style lang="scss" scoped>
.goods-single-row{
  display: flex;
  margin: 0 25px;
}
</style>



