<template>
  <div class="detailPage" v-show="detail.product">
    <headerNav v-show="pageStatus.isShowHead" 
      :shareShow="detail.product.selectedSku.shareInfo && detail.product.selectedSku.roleVo.shareable!=0" 
      :headerStyle="headerStyle"
      @double="doubleClick"
      @back="backToLastPage"
      @share="share" />
    <section ref="bobyBox" class="bodyBox scrollKeep bannedClick"  v-on:scroll.passive="pageScroll" :style="{overflowY:pageStatus.isShowSelect?'hidden':'scroll'}">
      <!-- 轮播图 -->
      <swipeBanner></swipeBanner>
      <div class="goods_title">
        <h2>{{this.detail.product&&this.detail.product.selectedSku&&this.detail.product.selectedSku.skuSubtitle}}</h2>
        <ul class="label_ul">
          <li v-for="(item, index) in this.detail.product.selectedSku.tag" :key="index">{{item.title}}</li>
        </ul>
        <div class="label_money" :style="isMoneyStyle">
          <span v-show="detail.product.payType=='credit'||detail.product.payType=='all'" class="integral">{{this.detail.product.selectedSku.price.currentUserPricePoint}}</span>
          <span v-show="detail.product.payType=='cash'||detail.product.payType=='all'" class="money">&yen;{{this.detail.product.selectedSku.price.currentUserPriceCash | precisionTwo}}</span>
        </div>
      </div>
      <div v-show="!isAllSoldOut" class="goods_select" @click="toggle(true)">
        <div class="goods_select_box">
          <i class="select_text">已选</i>
          <p class="text_change">
            {{detail.product.serialAttributes!=null&&detail.product.serialAttributes[0]?(pageStatus.skuSpec||"请选择... "):''}}数量X {{pageStatus.amount}}
            <!-- <span v-if="detail.product.serialAttributes">{{pageStatus.skuSpec||"请选择..."}}</span>
            <span>数量X {{pageStatus.amount}}</span> -->
          </p>
        </div>
        <div class="goods_select_hint">
          <i></i><span> 满 </span><span class="integral">990</span><span>（&yen;99）包邮</span>
          <i style="margin-left:30px"></i><span>车主免邮</span>
        </div>
      </div>
      <ul class="other_buttom" :style="{marginTop:!isAllSoldOut?0:'20px',borderTop:!isAllSoldOut?0:'1px solid #F0F1F2'}" v-if="comments.count>0">
         <li v-if="comments.count>0" @click="gotoUserComments">
          <span>用户评价 {{this.comments.count>999?"999+":this.comments.count}}</span>
        </li>
      </ul>
      <!-- 评论 -->
      <userComments />
      <!-- 文描组件 -->
      <describeBox :content="getMainDescription" />
      <ul class="other_buttom">
        <li v-for="(tab,index) in detail.product.selectedSku.description" :key="index" @click="gotoDescribe(tab)">
          <span>{{tab.tabName}}</span>
        </li>
      </ul>
      <!-- 设计师推荐 -->
      <!-- <recommend /> -->
      <IphonexBar />
    </section>
    <footer class="footer_nav" :style="{paddingBottom:isIphoneX?'36px':'12px'}">
      <div class="footer_nav_left">
        <div class="service_but" @click="gotoService"></div>
        <div class="shoppingcart_but" @click="gotoCart">
          <span v-show="cartCount>0" class="num">{{cartCount}}</span>  
          <!-- <mt-badge v-show="cartCount>0" class="num" size="small" color="#EF7575">{{cartCount}}</mt-badge> -->
        </div>
      </div>
      <div class="footer_nav_right">
        <div class="join_but" :class="getCartStatus" @click="addCart">
          <span>加入购物车</span>
        </div>
        <div class="buy_nut" :class="getBuyStatus" @click="gotoConfirmOrder">
          <span v-if="getIntegralNot">积分不足</span>
          <span v-else>立即购买</span>
        </div>
      </div>
    </footer>
    <!-- 规格选择弹窗遮罩 -->
    <div v-show="this.pageStatus.isShowSelect" class="specifications_figure"></div>
    <!-- 规格选择弹窗 -->
    <specifications ref="spec" v-bind:hubus="hubus" :is-show="this.pageStatus.isShowSelect" @closeSelect="toggle(false)" v-on:saveQueryParams="saveQueryParams"  v-on:loadDetail="loadDetail" />
    <!-- 添加购物车，动画小球 -->
    <MoveBall />
  </div>
</template>

<script>
  import Banner from './Banner.vue'
  import Specifications from './Specifications.vue'
  import Comments from './Comments.vue'
  import MoveBall from './MoveBall.vue'
  import headerNav from '@/components/header-nav.vue'
  import describeBox from '@/components/describe.vue'
  import IphonexBar from '@/components/iphonex-bar.vue'
  import {mapState, mapMutations, mapActions} from 'vuex'
  import Vue from 'vue'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import initWXShare from '@/util/initWXShare'
  import {supreme,baseMixin,scrollBehaviorMixin,scrollBannedClickMixin,type} from '@/supreme-core'
  import { detailBySaModel, addCartBySaModel,creditShareModel } from '@/model'
  import $ from 'webpack-zepto'

  var delay=0;
  export default {
    name: "Index",
    mixins: [baseMixin,scrollBehaviorMixin,scrollBannedClickMixin],
    components: {
      swipeBanner: Banner,
      userComments: Comments,
      specifications: Specifications,
      MoveBall: MoveBall,
      headerNav,
      describeBox,
      IphonexBar
    },
    data(){
      return{
        scrollTopNum:0,
        headerStyle:'',
        isLoading:false,
        decImagesArr:[],
        notStopClick:false,
        queryParams:{

        },
        butItem:[
        'getAccountToken',
        'getOSInfo',
        'Back', 
        'Toast',
        'showLoading',
        'hideLoading',
        'disableWebviewPull',
        'enableWebviewPull',
        'showImagesPreview',
        'isInWebview',
        'fetchCartSKUCount',
        'trackCustomKVEvent']
      }
    },
    appear(info){
      // supreme.showToast('goods page  appear...........');
      // 刷新数据
      this.loadDetail(this.queryParams);
    },
    networkChange(status){
      // nio://carmall.nrs/detail?type=${type}&code=${code}
      //supreme.showToast("networkChage:"+status);
    },
    created(){

      var self=this;
      // 开启规格层,关闭弹性
      util.isInWebview()&&hybrid.disableWebviewPull();
      util.isInWebview()&&hybrid.trackBeginPage();
      if(util.newVersion()){
        console.log('3.3.0')
      }else{  
        if(!util.isInWebview()){
          this.pageStatus.isShowHead=false;
        }
         // 购物车更改 刷新数据
        util.isInWebview()&&hybrid.tempControlCartBack({},function(){
          self.loadDetail(self.queryParams);
        })
        // 评论关注 刷新数据
        util.isInWebview()&&hybrid.userFocuseChange({},function(){
          self.loadDetail(self.queryParams);
        })
        // 页面开始埋点
      }
      // 获取数据
      if(supreme.localCache.get(supreme.getQueryString("spuCode")||supreme.getQueryString("skuCode"))){
        var resultData= supreme.localCache.get(supreme.getQueryString("spuCode")||supreme.getQueryString("skuCode"));
        supreme.hideLoading();
        this.setDetail(resultData);
        if(util.isInWebview()){
          this.pageStatus.isShowHead=true;
          this.closeArrows(0);
        }else{
          this.pageStatus.isShowHead=false;
        }

        // 处理分享微信头部显示
        var titleDoc = document.getElementsByTagName("title")[0];
        titleDoc.innerHTML = resultData.product.spuName;
      }
      this.loadData();
    },
    watch: {
      scrollTopNum(newval, oldVal){
        if(newval>10){
          this.headerStyle=`rgba(255, 255, 255, ${newval/400})`;

        }else if(newval>400){
          this.headerStyle='rgba(255, 255, 255, 1)';

        }else{
          this.headerStyle='linear-gradient(0deg,rgba(0,0,0,0),rgba(0,0,0,0))';
        }
      }
    },
    computed:{
      ...mapState({
        'detail': state => state.detailState.data,
        'cartCount': state => state.detailState.cartCount,
        'userCredits': state => state.detailState.userCredits,
        'comments': state => state.detailState.comments,
        'pageStatus':state => state.detailState.pageStatus,
        'selectedSku': state => state.detailState.data.product.selectedSku,
        'payModel':state => state.detailState.pageStatus.payModel
      }),
      // 是否全部售罄
      isAllSoldOut(){
        let isAllSoldOut = true;
        for(let i = 0; i < this.detail.product.skuList.length; i++){
          if(!this.detail.product.skuList[i].isSoldOut){
            isAllSoldOut = false;
          }
        }
        if(this.detail.product.skuList.length==0){
          isAllSoldOut=true;
        }
        return isAllSoldOut;
      },
      // 价格和文描的间隔线（无评论且已售罄显示）
      isMoneyStyle(){
        if(this.isAllSoldOut&&this.comments.count==0){
          return {
            borderBottom:'1px solid #F0F1F2',
            paddingBottom:'20px'
          }
        } 
      },
      isIphoneX(){
        return util.isIphoneX();
      },
      newVersion(){
        return util.newVersion();
      },
      //获取商品主描述
      getMainDescription(){
        return decodeURIComponent( this.selectedSku.content);
      },
      // 积分不足
      getIntegralNot(){
        if(this.selectedSku.roleVo.saleStatus==0){
          return false;
        }else if(util.isToken()&&this.detail.product.payType=='credit'&&this.userCredits<this.selectedSku.price.currentUserPricePoint){
          return true;
        }else if( util.isToken()&&this.detail.product.payType=='credit'&&this.userCredits<this.selectedSku.price.currentUserPricePoint*this.pageStatus.amount){
          return true;
        }else{
          return false;
        }
      },
      getCartStatus(){
        if(!this.selectedSku.available||this.selectedSku.isSoldOut){
          return "disabled"
        }else if(this.selectedSku.roleVo.saleStatus==0&&this.selectedSku.roleVo.showExclusive!=1){
          return "disabled"
        }else if(!this.pageStatus.canAddCart){
          return "disabled"
        }
      },
      getBuyStatus(){
        if(!this.selectedSku.available||this.selectedSku.isSoldOut){//是否售罄、下架
          return "disabled"
        }else if(this.selectedSku.roleVo.saleStatus==0&&this.selectedSku.roleVo.showExclusive!=1){//不可售状态、类型
          return "disabled"
        }else if(this.selectedSku.roleVo.showExclusive!=1&&util.isToken()&&this.detail.product.payType=='credit'&&this.userCredits<this.selectedSku.price.currentUserPricePoint*this.pageStatus.amount){
          // 纯积分商品数量增加 立即购买置灰 显示积分不足
          return "disabled"
        }else if(!this.pageStatus.canBuy){
          return "disabled"
        }
      },
      openServiceUrl(){
          let isiOS = navigator.userAgent.match('iPad') || navigator.userAgent.match('iPhone') || navigator.userAgent.match('iPod');
          
          let goodsStatus = "IN_SALES";
              // feePrice = this.detail.product.payType=="all"||this.detail.product.payType=="cash"?this.selectedSku.price.currentUserPriceCash:this.selectedSku.price.currentUserPricePoint;
          if(!this.selectedSku.available){//下架
            goodsStatus =  "BE_REMOVED"
          }else if(this.selectedSku.isSoldOut&&this.selectedSku.available){//售罄
            goodsStatus = "SOLD_OUT"
          }else{//在售
            goodsStatus =  "IN_SALES"
          }
          // this.detail.product.payType=="all"||this.detail.product.payType=="cash"?:"POINTS
          let track ={type:"TRACK_MER_GOODS"}
          let goodsParams = {
            "productID": this.detail.product.spuCode, 
            "remoteurl": this.selectedSku.pictureList[0].thumbnailURL,
            "title": this.selectedSku.skuSubtitle,
            "originPrice": this.selectedSku.price.originPricePoint,
            "feePrice": this.selectedSku.price.feePricePoint,
            "priceType": this.selectedSku.price.type,
            "priceLabel": this.selectedSku.price.label,
            "currency": "POINTS",
            "goodsType":"MALL_GIFT",
            "status":goodsStatus
          };
          let iosUrl = `nio://scr.im/customerservice?track=${encodeURI(JSON.stringify(track))}&goods=${encodeURIComponent(JSON.stringify(goodsParams))}`,
              androidUrl = `rong://cn.com.weilaihui3/conversation/customer_service?targetId=service&title=在线客服&track=${encodeURI(JSON.stringify(track))}&goods=${encodeURIComponent(JSON.stringify(goodsParams))}`;

          return isiOS?iosUrl:androidUrl;
      }
    },
    methods:{
      ...mapMutations({
        setDetail:"detailState/setDetail",
        browserOpenNIO:"detailState/browserOpenNIO",
        setCartCount:"detailState/setCartCount"
      }),
      ...mapActions({
        getCartCount: "detailState/getCartCount",
        getUserCredits: "detailState/getUserCredits",
        getDetailComments:"detailState/getDetailComments"
      }),
      testDsBridge(item){
        let dict = {"content": "显示消息"}
        let dictImages = {
            images: ["https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1544530799687&di=c84047a2d010acbed4f6042e133ef324&imgtype=0&src=http%3A%2F%2Fimg.mp.itc.cn%2Fupload%2F20160831%2F99c25d4e319f4634bc985340f7fee3dd_th.jpg",
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1544530799687&di=dbe4ae81c260213a7b564ecd52f17470&imgtype=0&src=http%3A%2F%2Fimg.alicdn.com%2Fi1%2FTB2uy9RppXXXXX5XXXXXXXXXXXX_%2521%2521660595271.jpeg"],
            index: 2
        };
        switch(item){
          case 'getAccountToken':
            let AccountToken = dsBridge.call("common.getUserInfo", "");
            if(AccountToken){
              alert(JSON.stringify(AccountToken))
              return;
            }
            dsBridge.call("NIODoBridge.openLoginPage", "", function(data){
              alert(data);
            });
            break;
          case 'Back':
            dsBridge.call("common.closeSyn");
            break;
          case 'Toast':
            dsBridge.call("common.showToast", dict)
            break;
          case 'showLoading':
            dsBridge.call("NIODoBridge.showLoading", "")
            setTimeout(()=>{
              dsBridge.call("NIODoBridge.hideLoading", "")
            },1000);
            break;
          case 'hideLoading':
            dsBridge.call("NIODoBridge.hideLoading", "")
            break;
          case 'disableWebviewPull':
            dsBridge.call("common.disableWebviewPull", "")
            break;
          case 'enableWebviewPull':
            dsBridge.call("common.enableWebviewPull", "")
            break;
          case 'showImagesPreview':
            dsBridge.call("NIODoBridge.showImagesPreview", dictImages)
            break;
          case 'getOSInfo':
            var info = dsBridge.call("common.getOSInfo", "");
            supreme.showToast(info)
          case 'fetchCartSKUCount':
            dsBridge.call("NIOMerBridge.fetchCartSKUCount", '')
            break;
          case 'trackCustomKVEvent':
            dsBridge.call("NIOMerBridge.trackCustomKVEvent", {
              eventKey:'MallItemPage_Service_Click',
              currentPage:'MallItemPage_',
              NextPage:'',
              params:{
                spu_code:supreme.getQueryString("spuCode")
              }
            })
            break;
          case 'isInWebview':
            supreme.showToast(util.isInWebView());
            break;
        }
      },
      pageScroll(event){
        this.scrollTopNum = event.target.scrollTop;
      },
      closeArrows(i){
        if(i<=5){
          setTimeout(()=>{
            i++
            hybrid.pageLoadFinish();
            hybrid.hideLoading();
            this.closeArrows(i);
          },500);
        }
      },
      // 保存重新选择后的查询参数
      saveQueryParams(params){
        supreme.$.extend(true,this.queryParams,params);
      },
      doubleClick(){
        this.$refs.bobyBox.scrollTop = 0;
      },
      toggle(flag){
        if(flag==undefined){
          this.pageStatus.isShowSelect=!this.pageStatus.isShowSelect
        }else{
          this.pageStatus.isShowSelect=flag;
        }
      },
      loadData(){
        var reqObj={};
        if(supreme.getQueryString("version")==2){
          if(supreme.getQueryString("spuCode")){
            reqObj={
              spuCode:supreme.getQueryString("spuCode")
            };
          }else if(supreme.getQueryString("skuCode")){
            reqObj={
              skuCode:supreme.getQueryString("skuCode")
            };
          }
        }else {
          // 分享模式进来
          if(supreme.getQueryString("spuCode")||supreme.getQueryString("skuCode")){
            reqObj={
              isSwc:true,
              swcId:supreme.getQueryString("spuCode")?supreme.getQueryString("spuCode"):supreme.getQueryString("skuCode")
            };
          }
        }
        var params={
          data:reqObj,
          first:true
        };
        this.saveQueryParams(params);
        this.loadDetail(params);
      },
      /**
       * @desc  加载商详信息
       * */
      loadDetail(params){
        if(type.isString(params["data"])){
          try{
            params["data"]=JSON.parse(params["data"]);
          }catch (ex){
            throw ex;
            return ;
          }

        }
        this.isLoading=true;
        // 请求商品详情
        detailBySaModel.execute(params).then(data=>{
          this.isLoading=false;
          let {resultData, resultDesc} = data.data;
          if(resultDesc=='success'){
            if(resultData.product.serialAttributes!=null){
              resultData.product.serialAttributes.forEach((attrs)=>{
                attrs.spuAttributeValueList.forEach((attr,index)=>{
                  attr.status="";
                  attr.imageStatus=""
                })
              })
            }
            this.pageStatus.canBuy=true;
            this.pageStatus.canAddCart=true;
            // 处理spu下部分sku售罄的逻辑
            for(var i=0;i<resultData.product.skuList.length;i++){
              var sku = resultData.product.skuList[i];
              if(sku.isSoldOut){
                resultData.product.skuList.splice(i,1);
                i--;
              }
            }
            // 设置详情数据
            this.setDetail(resultData);
            // 拉取商品评论
            this.getDetailComments();
            if(util.isToken()){
              // 获取购物车数量
              this.getCartCount();
              // 获取用户积分
              this.getUserCredits()
            }
            if(params.first){
              supreme.localCache.set(supreme.getQueryString("spuCode")||supreme.getQueryString("skuCode"),resultData);
            }
            supreme.localCache.set("description",resultData.product.selectedSku.description);
            var titleDoc = document.getElementsByTagName("title")[0];
            titleDoc.innerHTML = resultData.product.spuName;
            if(util.isInWebview()){
              this.pageStatus.isShowHead=true;
              this.closeArrows(0);
            }else{
              this.pageStatus.isShowHead=false;
              supreme.hideLoading();
            }
            if(this.selectedSku.shareInfo!=null && this.selectedSku.roleVo.shareable==1){
              // set微信分享
              initWXShare.WXshare({
                title: this.selectedSku.shareInfo.mainTitle,
                desc: this.selectedSku.shareInfo.subTitle ,
                link:`https://m-mercury.nio.com/detail?spuCode=${supreme.getQueryString("spuCode")}&version=2&share=true`,
                imgUrl:this.selectedSku.shareInfo.picturePath,
              });
            }
          }else{
            this.goto('/error',{message: data.data.message, resultDesc:data.data.resultDesc})
          }
        }).catch(err=>{
          //this.isLoading=false;
          this.pageStatus.canBuy=false;
          this.pageStatus.canAddCart=false;
          if(util.isInWebview()){
            hybrid.hideLoading();
          }else{
            supreme.hideLoading();
          }
          console.log('数据加载出错：'+err)
          if(err=='Network Error'){
            this.goto('/error',{message: 'Network Error'})
          }
        });
      },
      /**
       * @desc 获取跳转收银台使用的数据
       * @return object 获取要跳转页面所带的参数
       * */
      getCashierDeskData(){
        var payModel={
          "mode": "credit",  // 付款模式
          "value": {
            "credit": 0 // 实际价格
          }
        };
        for(var model in this.pageStatus.payModel){
          var m=this.pageStatus.payModel[model];
          if(m.status=="selected"){
            payModel.mode=model;
            if(model=="credit"){
              var point = parseFloat(this.selectedSku.price.currentUserPricePoint)
              payModel.value.credit=this.pageStatus.amount*point;
            }else if(model=="mix"){
              var point = parseFloat(this.userCredits);
              payModel.value.credit=point;

              var cash =parseFloat( this.selectedSku.price.currentUserPriceCash)*this.pageStatus.amount;
              var point =parseFloat(this.userCredits)/10;
              var money =parseFloat(this.selectedSku.price.currentUserPriceCash) ;
              payModel.value.money=cash-point;;
            }else if(model="money"){
              var money =parseFloat(this.selectedSku.price.currentUserPriceCash) ;
              payModel.value.money=this.pageStatus.amount*money;
            }
          }
        }
        var multiPorpArray=[];
        var sku = this.$refs.spec.getSelectedSku();
        if(sku){
          var selectedAttrs = this.$refs.spec.getSelectAttrs();
          for(var attr of selectedAttrs){
            if(attr.multiProp){
              multiPorpArray.push({
                attributeCode:attr.attributeCode,
                valueCode:attr.valueCode
              })
            }
          }
        }
        return {
          "origin_total_price":this.detail.product.selectedSku.price.originPricePoint*this.pageStatus.amount, // 原价  都是积分的形式
          "fee_value": this.detail.product.selectedSku.price.currentUserPricePoint*this.pageStatus.amount,  // 实际付款 折扣价
          "entrance": "goodsdatil", // 来源
          "exchange_rate": this.detail.product.exchangeRate, // 比例
          "payment_mode":payModel,
          "goods_skus": [
            {
              "sku_code": this.detail.product.selectedSku.skuCode,
              "spu_code": this.detail.product.spuCode, // 商品ID
              "amount": this.pageStatus.amount, //数量
              "item_name": this.detail.product.selectedSku.skuTitle, // 商品名称 title
              "sku_spec": this.pageStatus.skuSpec, // 拼接过商品规格选择
              //"credit": this.detail.product.selectedSku.price.fee_price, // 积分 实际单价
              //"origin_price": this.detail.product.selectedSku.price.origin_price, // 单价 原价
              "price":this.detail.product.selectedSku.price,
              "cover_image": this.detail.product.selectedSku.pictureList&&this.detail.product.selectedSku.pictureList.length&&this.detail.product.selectedSku.pictureList[0].thumbnailURL,
              "remain": this.detail.product.selectedSku.remain,  // 库存
              "available": this.detail.product.selectedSku.available, // 商品是否有效
              "limited_amount":this.detail.product.spuLimitedAmount,  //  限制购买数量
              "sku_limited_amount": this.detail.product.selectedSku.skuLimitedAmount, // 限制购买数量sku 级
              "pay_type": this.detail.product.payType, //  商品付款模式 cash credit all
              "specialAttributes":multiPorpArray,
              "remarks":this.detail.product.selectedSku.remarks//预售说明
            }
          ]
        }
      },
      /**
       *
       * "imageUrl": "https://cdn-app-test.nio.com/user/2017/9/15/cafd7657-aac9-49d0-86aa-4d914a966c35.jpg",
       "name":"美食",
       "score":"403",
       "shareUrl":"https://app-dev.nio.com/mall/item/676",
       "shareDesc": { "title": "title",
                                    "description": "description" }}
       *
       * */
      share(){
        if(util.isInWebview()){
          // 条件成立按钮隐藏
          // if( !this.selectedSku.shareInfo || this.selectedSku.roleVo.shareable==0){
          //   hybrid.showToast({content:"不支持分享"});
          //   return;
          // }
          // 页面分享按钮埋点
          hybrid.buriedPointClick({
            eventKey:'MallItemPage_Share_Click',
            currentPage:'MallItemPage_',
            NextPage:'',
            params:{
              spu_code:supreme.getQueryString("spuCode")
            }
          })
          if(this.newVersion){
            let sharePlat ='',
            shareUrl=`${util.getEnvInfo().openUrl}/detail?spuCode=${supreme.getQueryString("spuCode")}&version=2&share=true`;
            dsBridge.call("NIODoBridge.share", {
              title: this.selectedSku.shareInfo&&this.selectedSku.shareInfo.mainTitle,
              subtitle: this.selectedSku.shareInfo.subTitle ,
              imgUrl: this.selectedSku.shareInfo.picturePath,
              urlScheme: shareUrl,
            },(sharedata)=>{
              if(sharedata.result=='fail'){
                hybrid.showToast({content:"分享失败"})
                return;
              }else if(sharedata.result=='cancel'){
                hybrid.showToast({content:"分享取消"})
                return;
              }
              // 分享渠道
              if(sharedata.plat=='niofriend'){
                hybrid.showToast({content:"分享成功"})
                return;
              }else if(sharedata.plat=='weibo'){
                sharePlat = 'weibo';
              }else if(sharedata.plat=='wechatfriend'||sharedata.plat=='wechatcircle'){
                sharePlat = 'weixin';
              }
              // weixin/weibo 成功发起请求
              creditShareModel.post({data:{type:'share_goods',
                data:{
                  goods_id:supreme.getQueryString("spuCode"),
                  goods_name:this.selectedSku.shareInfo&&this.selectedSku.shareInfo.mainTitle,
                  channel:'weixin'
                }
              }}).then((result)=>{
                let {resultDesc, resultData} = result.data;
                if(resultDesc!='success')return;
                if(resultData.credit>0){
                  hybrid.showToast({content:`分享成功，+${resultData.credit}积分`})
                }else{
                  hybrid.showToast({content:'分享成功'})
                }
              })
            });
            return;
          }
          hybrid.share({
            "type": "share_goods",//商品分享传"share_goods",文章分享传"share_news"
            "id": this.detail.product.spuId, // 商品ID
            "imageUrl": this.selectedSku.shareInfo.picturePath, // 图片连接
            "name": this.selectedSku.shareInfo&&this.selectedSku.shareInfo.mainTitle, // 商品名称
            "score": this.detail.product.discountCredit, // 商品价格
            "shareUrl": location.href+"&share=true", // 商品连接
            "shareDesc": {
              "title": this.selectedSku.shareInfo&&this.selectedSku.shareInfo.mainTitle, //主标题
              "description": this.selectedSku.shareInfo.subTitle //副标题
            }
          },function(){

          });
        }
      },
      //如果应用是应hybrid 形式回退的名字需要是固定backToLastPage
      backToLastPage(){
        if(util.isInWebview()){
          // 页面返回按钮埋点
          hybrid.buriedPointClick({
            eventKey:'MallItemPage_Back_Click',
            currentPage:'MallItemPage_',
            NextPage:'',
            params:{
              spu_code:supreme.getQueryString("spuCode")
            }
          })
          // 页面离开埋点
          hybrid.trackEndPage();
          // 关闭当前页面
          hybrid.close();
        }
      },
      //
      gotoDescribe(item){
        // 暂未传递参数，区分
        if(util.isInWebview()){
          if(this.newVersion){
            let productParametersUrl = `${util.getEnvInfo().openUrl}${util.basePath()}/product_parameters?tabid=${item.tabId}&id=${this.queryParams.data.skuCode||this.queryParams.data.spuCode}&header=${encodeURIComponent('{"isShow":false}')}&wv=do`;
            // alert(productParametersUrl)
             hybrid.openPage({type:1, shema:productParametersUrl})
             return;
          }
          hybrid.openPage({
            type:0,
            url:"product_parameters",
            params:{
              isHideNavBar:true,
              tabid:item.tabId,
              id:this.queryParams.data.skuCode||this.queryParams.data.spuCode
            }
          });
        }else{
          this.goto("product_parameters",{tabid:item.tabId});
        }
      },
      // 跳转用户评价
      gotoUserComments(){
        if(util.isInWebview()){
          // 跳转用户评价埋点
          hybrid.buriedPointClick({
            eventKey:'MallItemPage_UserPost_Click',
            currentPage:'MallItemPage_',
            NextPage:'MallCommetPage_',
            params:{
              spu_code:supreme.getQueryString("spuCode")
            }
          })
          hybrid.openPage({type:1,shema:`nio://mall/comment/list?itemID=${this.selectedSku.skuSwcId}`})
        }else{
          this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
        }
      },
      //跳转客服
      gotoService (){
        if(util.isInWebview()){
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          // 跳转客服埋点
          hybrid.buriedPointClick({
            eventKey:'MallItemPage_Service_Click',
            currentPage:'MallItemPage_',
            NextPage:'',
            params:{
              spu_code:supreme.getQueryString("spuCode")
            }
          })
          // 页面离开埋点
          hybrid.trackEndPage();
          // 跳转客服
          hybrid.openPage({type:1, shema:this.openServiceUrl}); 
        }else{
          this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
        }
      },
      // 添加产品到购物车
      addCart(){
        // 判断Token 是否登录
        if(util.isInWebview()){
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          if (this.getCartStatus == "disabled" || this.isLoading) {
            return;
          }
          if(this.selectedSku.roleVo.showExclusive==1&&this.selectedSku.roleVo.saleStatus==0){
            hybrid.showToast({content:"该商品仅支持车主购买"});
            return;
          }
          // 预售商品不支持添加购物车
          // if(this.detail.product.selectedSku.presell==1){
          //   hybrid.showToast({content: "预售商品仅支持直接下单"});
          //   return ;
          // }
          var multiPorpArray=[];
          if(this.detail.product.serialAttributes&&this.detail.product.serialAttributes.length>0){
            // 多重判断降低添加错误产品的几率
            var sku = this.$refs.spec.getSelectedSku();
            if(sku){
              var selectedAttrs = this.$refs.spec.getSelectAttrs();
              for(var attr of selectedAttrs){
                if(attr.multiProp){
                  multiPorpArray.push({
                    attributeCode:attr.attributeCode,
                    valueCode:attr.valueCode
                  })
                }
              }
            }
            if(!sku||sku.isSoldOut||!sku.available){
              hybrid.showToast("请重新选择产品规格");
              return ;
            }
          }


          // 加入购物车埋点
          hybrid.buriedPointClick({
            eventKey: 'MallItemPage_AddCart_Click',
            currentPage: 'MallItemPage_',
            NextPage: '',
            params: {
              spu_code: supreme.getQueryString("spuCode")
            }
          })
          if (!this.pageStatus.isShowSelect) {
            this.toggle(true);
            return;
          }

          let params = {
            data: {
              skuCode: this.detail.product.selectedSku.skuCode,
              amount: this.pageStatus.amount,
              specialAttributes:multiPorpArray
            }
          }

          addCartBySaModel.execute(params).then(data => {
            let {resultDesc,resultData} = data.data;
            if (resultDesc == 'success') {
              // 开启加入购物车动画
              this.pageStatus.joinAnimation = true;
              this.setCartCount(resultData.cartCount);
              // 更新RN购物车角标
              hybrid.fetchCartSKUCount();
              hybrid.showToast({content: "加入购物车成功"});
            } else {
              hybrid.showToast({content: "加入购物车失败"});
            }
          }).catch(err => {
            hybrid.showToast({content: "加入购物车失败"});
            console.log('加入购物车出错:' + err)
          });
        }else{
          this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
        }
      },
      // 跳转购物车页面
      gotoCart(){
        if(util.isInWebview()){
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          // 跳转购物车埋点
          hybrid.buriedPointClick({
            eventKey:'MallItemPage_Cart_Click',
            currentPage:'MallItemPage_',
            NextPage:'MallCartPage_',
            params:{
              spu_code:supreme.getQueryString("spuCode")
            }
          })
          // 页面离开埋点
          hybrid.trackEndPage();
          // 跳转购物车
          hybrid.openPage({type:1,shema:'nio://cart'})
        }else{
          this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
        }
      },
      // 立即购买 跳转订单确定页
      gotoConfirmOrder(){
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          if(this.getBuyStatus=="disabled"||this.isLoading){
            return ;
          }
          if(this.selectedSku.roleVo.showExclusive==1&&this.selectedSku.roleVo.saleStatus==0){
            hybrid.showToast({content:"该商品仅支持车主购买"});
            return;
          }
          if(this.detail.product.serialAttributes&&this.detail.product.serialAttributes.length>0) {
            // 多重判断降低添加错误产品的几率
            var sku = this.$refs.spec.getSelectedSku();
            if (!sku || sku.isSoldOut || !sku.available) {
              hybrid.showToast({content:"请重新选择产品规格"});
              return;
            }
          }
          // 立即购买埋点
          hybrid.buriedPointClick({
            eventKey:'MallItemPage_Purchaser_Click',
            currentPage:'MallItemPage_',
            NextPage:'',
            params:{
              spu_code:supreme.getQueryString("spuCode")
            }
          })
          if(this.pageStatus.isShowSelect){
            if(this.notStopClick)return;//阻止重复点击
            this.notStopClick = true;
            if(util.newVersion()){
              hybrid.openPage({type:1,shema:`nio://mall/order.confirm?data=${encodeURIComponent(JSON.stringify(this.getCashierDeskData()))}`});
              this.notStopClick = false;
              return;
            }
            hybrid.openPage({type:1,shema:'orderConfirm',intent:this.getCashierDeskData()},(data)=>{
              this.notStopClick = false;
            })
          }else{
            this.toggle(true);
          }

        }else{
          this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
        }
      }
    }
  }
</script>

