<template>
   <h3 class="title" :style="{borderBottom:getTitleText.haveLine&&'1px solid #F0F1F2'}">{{getTitleText.data.title}}</h3>
</template>
<script>
  import { mapState, mapMutations } from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import {supreme,getCookie} from '@/supreme-core'
  export default {
    name: "ArticleTitle",
    computed:{
      ...mapState({
        'articleDetail': state => state.articleState.data,
      }),
      getTitleText(){
        let titleData = {};
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='title'){
            titleData = item;
          }
        })
        document.title = titleData.data.title;
        return titleData;
      }
    },
    methods:{
      
    }
  }
</script>

<style lang="scss" scoped>
  .title{
    margin:20px 25px;
    color: #040B29;
    font-size: 24px;
    line-height: 30px;
    background-color: #fff;
    overflow: hidden;
    text-overflow: ellipsis;
    display:-webkit-box;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    -webkit-line-clamp:3; //显示的
  }
</style>



