import gesture from "../../../util/gesture";

/**
 * @description 手势处理回退，页面引入即有了通过手势回退的功能
 * @author lei.you
 * @date 2018/3/8
 * */
require("../../../util/hammer")
import {type} from '../../../util/util'
export default {
  mounted(){
    var hammer = new Hammer(this.$el);
    this.isTouchDirection(hammer);
    this.initBack(hammer);
    this.tempDirectionMethod=null;
    this.startPoint=null;
  },
  methods:{
    initBack(hammer){
      hammer.on("panstart panend ", ev=>{
        if(ev.type=="panend"){
          if(this.isBackGesture()){
            if(type.isFunction(this.backToLastPage)){
              this.backToLastPage();
            }
          }
          this.tempDirectionMethod=null;
          this.startPoint=null;
        }

        if(ev.type=="panstart"){
          this.startPoint=ev.center;
        }
      });
    },
    // touch 的方向
    isTouchDirection(hammer){
      hammer.on("panleft panright panup pandown", ev=>{
        console.log(ev.type);
        if(ev.type=="panleft"){
          this.tempDirectionMethod=gesture.TOUCH_LEFG_METHOD;
        }else if(ev.type=="panright"){
          this.tempDirectionMethod=gesture.TOUCH_RIGHT_METHOD;
        }else if(ev.type=="panup"){
          this.tempDirectionMethod=gesture.TOUCH_UP_METHOD;
        }else if(ev.type=="pandown"){
          this.tempDirectionMethod=gesture.TOUCH_DOWN_METHOD;
        }

      });
    },
    //判断用户是不是在做回退手势
    isBackGesture(){
      return this.startPoint.x<30&&this.tempDirectionMethod==gesture.TOUCH_RIGHT_METHOD;
    }

  }

}



