/**
 * @exports reactNativeHybrid
 * @desc 对reactNative hybrid 特殊封装，mer精品首次上线临时使用
 * @date 2018/07/09
 * @author lei.you
 * */
//var postMsg = window.postMessage;
import {type} from '@/supreme-core'
import {supreme} from '@/supreme-core'
import util from '@/util/util'
export default{
  hasInitlized:false,
  suffixMethod:"rndo_",
  maxCallCount:0,
  /**
   * @access private
   * @desc 私有方法，处理参数后调用postMessage
   * @param {object}
   * */
  _postMsg(method,params,callback){
    if(type.isFunction(params)){
      callback=params;
      params={};
    }
    var postParams={
      method,
      callbackMethod:this.suffixMethod+method,
      params
    }
    try{
      //console.log(postParams);
      this._registerCallback(postParams,callback) ;
      window.postMessage(JSON.stringify(postParams));
    }catch(ex){
      //通过可恢复异常处理延迟问题
      setTimeout(e=>{
        //console.log('___________________________________'+method+"_____"+callback);
        this._postMsg(method,params,callback);
      },100);

    }

  },
  /**
   * @access private
   * @desc 把callback 注册到一个全局变量中
   * @param {object}
   * */
  _registerCallback(postParams,callback){
    //this._postMsg("console",window._mer)

    if(callback){
      window[postParams.callbackMethod] = callback;
    }else{
      delete postParams.callbackMethod;
    }

  },
  /**
   * @access public
   * @desc 新打开页面
   * @param {object}  {
   *                      type:0 number// 0:新打开webview应用页面 ,1:新打开native应用页面
   *                      url: string// type 0 时候生效
   *                      params: object url 携带的参数
   *                      nio://mall/comment/list?itemID=${itemId} //
   *                      shema:string //  type 1 时候生效 // login 登录页面 , cashierDesk 收银台页面 ,shopCar 购物车页面
   *                      intent :object// type 1 时候生效
   *                    }
   *
   * */
  openPage(params={type:0,url:''},callback){
    params.type==undefined&&(parmas.type=0);
    params.type==0&&(function(){
      if(params.url.indexOf("http")==-1&&params.url.indexOf("file")==-1){
        var protocol = location.protocol;
        var host = location.host;
        var href= location.href;
        var origin = location.origin
        if(protocol=="http:"||protocol=="https:"){
          if(type.isObject(params.params)){
            if(location.hash){
              params.url=href.replace(location.hash,"#/"+params.url+"?"+supreme.$.param(params.params))
            }else{
              params.url=origin+"/"+params.url+"?"+supreme.$.param(params.params);
            }
          }else{
            if(location.hash){
              params.url=href.replace(location.hash,"#/"+params.url);
            }else{
              params.url=origin+"/"+params.url;
            }
          }

        }
        if(protocol=="file"){

        }
      }

    }());
    if(util.newVersion()){
      dsBridge.call("NIODoBridge.openURL", params.shema);
      return;
    }
    this._postMsg("openPage",params,callback);
  },

  /**
   * @access public
   * @desc 分享
   * @param {object} 参数 {
          "id":, // 商品ID
          "imageUrl": , // 图片连接
          "name":, // 商品名称
          "score":, // 商品价格
          "shareUrl":, // 商品连接
          "shareDesc": {
            "title": "title...........", //
            "description": "description.................." //
          }
  }
   * @param {function} 回调
   * */
  share(params={},callback){
    this._postMsg("share",params,callback);
  },
  /**
   * @access public
   * @desc 关闭自身activity
   * @param {object} 参数
   * @param {function} 回调
   * */
  close(params={},callback){
    if(util.newVersion()){
      dsBridge.call("common.closeSyn");
    }else{
      this._postMsg("close",params,callback);
    }
  },
  /**
   * @access public
   * @desc click事件埋点
   * @param {object} 参数
   * @param {function} 回调
   * */
  trackCustomKVEvent(params={},callback){
    if(util.newVersion()){
      if(!dsBridge.hasNativeMethod("NIOMerBridge.trackCustomKVEvent"))return;
      dsBridge.call("NIOMerBridge.trackCustomKVEvent", params);
    }else{
      this._postMsg("trackCustomKVEvent",params,callback);
    }
  },
  /**
   * @access public
   * @desc click埋点公用方法
   * @param {parameters} 参数
   * */
  buriedPointClick(parameters){
    let {eventKey, currentPage, NextPage, params} = parameters;
    this.trackCustomKVEvent({
      eventKey:eventKey,
      data:{
        Path:{
          current_page:currentPage,
          Next_page:NextPage
        },
        parameter:{
          ...params
          // Spu_code:supreme.getQueryString("spuCode")
        }
      }
    })
  },
  /**
   * @access public
   * @desc 页面开始埋点(用于获取用户停留时长)
   * @param {object} 参数
   * @param {function} 回调
   * */
  trackBeginPage(params={pageName: "MallItemPage_"},callback){
    if(util.newVersion()){
      if(!dsBridge.hasNativeMethod("NIOMerBridge.trackBeginPage"))return;
      dsBridge.call("NIOMerBridge.trackBeginPage", params);
    }else{
      this._postMsg("trackBeginPage",params,callback);
    }
  },
  /**
   * @access public
   * @desc 页面结束埋点(用于获取用户停留时长)
   * @param {object} 参数
   * @param {function} 回调
   * */
  trackEndPage(params={pageName: "MallItemPage_Time"},callback){
    if(util.newVersion()){
      if(!dsBridge.hasNativeMethod("NIOMerBridge.trackEndPage"))return;
      dsBridge.call("NIOMerBridge.trackEndPage", params);
    }else{
      this._postMsg("trackEndPage",params,callback);
    }
  },
  /**
  * @access public
  * @desc 更新RN list页购物角标
  * @param {object} 参数
  * @param {function} 回调
  * */
   fetchCartSKUCount(params={},callback){
    if(util.newVersion()){
      dsBridge.call("NIOMerBridge.fetchCartSKUCount", "");
    }else{
      this._postMsg("fetchCartSKUCount",params,callback);
    }
   },
   /**
   * @access public
   * @desc 通知RN webview h5加载完成
   * @param {object} 参数
   * @param {function} 回调
   * */
    pageLoadFinish(params={},callback){
      this._postMsg("pageLoadFinish",params,callback);
    },
   /**
   * @access public
   * @desc 跳转登录页
   * @param {object} 参数
   * @param {function} 回调
   * */
    openLoginPage(params={},callback){
      if(util.newVersion()){
        dsBridge.call("NIODoBridge.openLoginPage", "", function(data){
          if(data['isSuccessed']){}
        });
      }else{
        this._postMsg("openLoginPage",params,callback);
      }
    },
  /**
   * @access public
   * @desc showLoading
   * @param {object} 参数 {
            "content":"xxx" //loading 内容
        }
   * @param {function} 回调
   * */
  showLoading(params={},callback){
    if(util.newVersion()){
      dsBridge.call("NIODoBridge.showLoading", "")
    }else{
      this._postMsg("showLoading",params,callback);
    }
  },
  /**
   * @access public
   * @desc hideLoading
   * @param {object} 参数
   * @param {function} 回调
   * */
  hideLoading(params={},callback){
    if(util.newVersion()){
      dsBridge.call("NIODoBridge.hideLoading", "")
    }else{
      this._postMsg("hideLoading",params,callback);
    }
  },
  /**
   * @access public
   * @desc showToast
   * @param {object} 参数 {
            "content":"xxx" //toast内容
        }
   * @param {function} 回调
   * */
  showToast(params={},callback){
    if(util.newVersion()){
      dsBridge.call("common.showToast", params)
      return;
    }
    if(type.isString(params)){
      this._postMsg("showToast",{content:parmas},callback);
    }else if(type.isObject(params)){
      if(!params.hasOwnProperty("content")){
        this._postMsg("showToast",{content:JSON.stringify(params)},callback);
        return ;
      }
      this._postMsg("showToast",params,callback);
    }else{
      throw("argument is illege");
    }

  },
  /**
   * @access public
   * @desc 跳转图片预览
   * @param {object} 参数 {
        "images":[
          https://xxx.jpg, https://xxx.jpg
        ],
        "index":0
    }
   * @param {function} 回调
   * */
  showImagesPreview(params={},callback){
    if(util.newVersion()){
      dsBridge.call("NIODoBridge.showImagesPreview", params)
    }else{
      this._postMsg("showImagesPreview",params,callback);
    }
  },
  /**
   * @access public
   * @desc 禁止页面下拉（皮筋效果）
   * @param {object} 参数
   * @param {function} 回调
   * */
  disableWebviewPull(params={},callback){
    if(util.newVersion()){
      if(dsBridge.hasNativeMethod("common.disableWebviewPull")){
        dsBridge.call("common.disableWebviewPull", "")
      }
    }else{
      this._postMsg("disableWebviewPull",params,callback);
    }
  },
  /**
   * @access public
   * @desc 开启页面下拉（皮筋效果）
   * @param {object} 参数
   * @param {function} 回调
   * */
  enableWebviewPull(params={},callback){
    if(util.newVersion()){
      if(dsBridge.hasNativeMethod("common.enableWebviewPull")){
        dsBridge.call("common.enableWebviewPull", "")
      }
    }else{
      this._postMsg("enableWebviewPull",params,callback);
    }
  },
  /**
   * @access public
   * @desc 处理返回键
   * @param {object} 参数
   * @param {function} 回调
   * */
  controlBackPress(params={isControl:false},callback){
    this._postMsg("controlBackPress",params,callback);
  },
  /**
   * @access public
   * @desc 临时解决购物车回退的问题
   * @param {object} 参数
   * @param {function} 回调
   * */
  tempControlCartBack(params={},callback){
    // this.controlWebviewAppear.name 这种方式特殊的手机系统下不能拿到值
    this._postMsg("tempControlCartBack",params,callback);
  },
  /**
   * @access public
   * @desc 全部评论回退(与购物车相似)
   * @param {object} 参数
   * @param {function} 回调
   * */
  userFocuseChange(params={},callback){
    this._postMsg("userFocuseChange",params,callback);
  },
  /**
   * @access public
   * @desc 拷贝到剪切板
   * @param {object} 参数
   * @param {function} 回调
   * */
  copyText(params={},callback){
    this._postMsg("copyText",params,callback);
  },
  /**
   * @access public
   * @desc 拷贝到剪切板
   * @param {object} 参数
   * @param {function} 回调
   * */
  jumpArticleCommentsDetail(params={},callback){
    this._postMsg("jumpArticleCommentsDetail",params,callback);
  },
  /**
   * @access public
   * @desc 禁止手势回退
   * @param {object} 参数
   * @param {function} 回调
   * */
  disableGestureBack(params={},callback){
    this._postMsg("disableGestureBack",params,callback);
  },
  /**
   * @access public
   * @desc 启用手势回退
   * @param {object} 参数
   * @param {function} 回调
   * */
  enableGestureBack(params={},callback){
    this._postMsg("enableGestureBack",params,callback);
  },
  /**
   * @desc 获取用户信息
   * @param {object} 参数
   * @param {function} 回调
   * */
  getUserInfo(params={},callback){
    this._postMsg("getUserInfo",params,callback);
  },
  /**
   * @access public
   * @desc 获取os 信息
   * @param {object} 参数
   * @param {function} 回调
   * */
  getOSInfo(params={},callback){
    this._postMsg("getOSInfo",params,callback);
  },
}



