import {BaseStore} from '@/supreme-core';
import {cartGetcartCountModel, userCreditsModel, detailCommentListModel} from '@/model'

let state = {
  pageStatus:{
    amount:1,
    skuSpec:"",
    canBuy:true,
    canAddCart:true,
    scrollTopNum:0,
    isShowSelect:false,
    joinAnimation:false,//加入购物车动画开关,
    isShowHead:false,// 是否显示头部，默认显示
    isToastShow:false,
    addToast:false,
    payModel:{
      credit:{
        status:'' // selected "" disabled
      },
      mix:{
        status:''
      },
      money:{
        status:''
      }
    }
  },
  // 商品数据
  data: {

  },
  // 购物车数量
  cartCount:0,
  // 用户积分
  userCredits:0,
  // 商品评论
  comments:{
    count:0,
    list:[]
  },
}
const attrCls={
  disable:"disabled",
  selected:"selected",
  normal:"",
  imageSelect:"border-color:#00BCBC;",
  imageNormal:"border-color:#FAC261;",
  imageDisable:"border-color:#cdced4;"
};
let mutations = {
  //初始化数据
  setDetail(state,data){
    //supreme.$.extend()
    try{
      if(state.data.product&&data.product.serialAttributes){
        for(var attrGroup of  data.product.serialAttributes){
          if(attrGroup.spuAttributeValueList){
            for(var attr of attrGroup.spuAttributeValueList){
              var ar= mutations._getAttrByValueCode(attr.valueCode,state.data.product);
              attr.status=ar.status;
              attr.imageStatus=ar.imageStatus;
            }
          }
        }
      }
    }catch (ex){
      state.data = data
    }


    state.data = data
  },
  setCartCount(state,data){
    state.cartCount = data;
  },
  setUserCredits(state,data){
    state.userCredits = data;
  },
  setComments(state,data){
    state.comments = data;
  },
  //通过valueCode 获取属性
  _getAttrByValueCode(valueCode,product){

    var attribute;
    c:for(var attrGroup of  product.serialAttributes){
      for(var attr of attrGroup.spuAttributeValueList){
        if(attr.valueCode==valueCode){
          attribute=attr;
          break c;
        }
      }
    }
    return attribute;
  },
  // 更新选中状态
  updateAttrSelectStatus(state,data){
    state.data.product.serialAttributes.forEach((attrs)=>{
      attrs.spuAttributeValueList.forEach((attr,index)=>{

        if(data.attr.valueCode==attr.valueCode){
          attrs.spuAttributeValueList.forEach((ar)=>{
            ar.status="";
          })
          attr.status=data.status;
        }
      })

    })

  },
  browserOpenNIO(state, data){
    var isiOS = navigator.userAgent.match('iPad') || navigator.userAgent.match('iPhone') || navigator.userAgent.match('iPod'),
      isAndroid = navigator.userAgent.match('Android');
    var ua = navigator.userAgent.toLowerCase();
    if (ua.match(/MicroMessenger/i) == 'micromessenger') { // 微信浏览器判断
      state.pageStatus.isToastShow = true;
      return;
    } else if (ua.match(/WeiBo/i) == "weibo") {
      state.pageStatus.isToastShow = true;
      return;
    }
    if(isiOS){
        setTimeout(function () {
            window.location = "https://itunes.apple.com/cn/app/wei-lai/id1116095987?mt=8";
        }, 2000);
        window.location = data;
    }else {
      var timeout = 3000;
      var isBlur = false;//页面失焦
      var ifr = document.createElement('iframe');
      ifr.src = data;
      ifr.style.display = 'none';
      document.body.appendChild(ifr);
      setTimeout(function() {
        if(!isBlur){
          document.body.removeChild(ifr);
          window.location.href = 'https://app.nio.com/download';
        }
      }, timeout);
      // 监听页面不可见
      window.onblur = function(){
        isBlur = true;
      };
      var hiddenProperty = 'hidden' in document ? 'hidden' :
          'webkitHidden' in document ? 'webkitHidden' :
          'mozHidden' in document ? 'mozHidden' :
          null;
      var  visibilityChangeEvent = hiddenProperty.replace(/hidden/i, 'visibilitychange');
      var onVisibilityChange = function(){
        if(document[hiddenProperty]){
          isBlur = true;
        }
      }
　　  document.addEventListener(visibilityChangeEvent, onVisibilityChange);
    }

  }
}
let actions = {
  getCartCount({ commit }){
    cartGetcartCountModel.execute({}).then((data)=>{
      let {resultDesc, resultData} = data.data;
      if(resultDesc=='success'){
        commit('setCartCount', resultData.cartCount)
      }
    })
  },
  getUserCredits({ commit }){
    userCreditsModel.execute({}).then((data)=>{
      let {resultDesc, resultData} = data.data;
      if(resultDesc=='success'){
        commit('setUserCredits', resultData);
      }
    })
  },
  getDetailComments({commit,state}){
    // alert(state.data.product.selectedSku.skuSwcId)
    detailCommentListModel.execute({data:{skuSwcId:state.data.product.selectedSku.skuSwcId}}).then((data)=>{
      let {resultDesc, resultData} = data.data;
      if(resultDesc=='success'){
        commit('setComments', resultData);
      }
    })
  }
};
var store= BaseStore.getInstance({
  state: state,
  mutations:mutations,
  actions:actions
});
export default {
  namespaced: true,
  state: state,
  mutations: mutations,
  actions: actions,
}



