import {BaseModel} from '@/supreme-core'
var generateSign = require('@/util/generateSign')
import util from '@/util/util'
var envInfo=util.getEnvInfo()
class LoginModel extends BaseModel{
  constructor(config){
    config.baseURL=config.baseURL|| envInfo.baseUrl;
    super(config);
  }
  post(param){
    if(!param){
      param={};
    }
    var signObj = generateSign.default()
    if(param.data){
      param.data["application"]=envInfo.application;
      param.data["timestamp"]=signObj.timestamp;
      param.data["digest"]=signObj.getwaySign;
      return super.post(param);
    }
    param["application"]=envInfo.application;
    param["timestamp"]=signObj.timestamp;
    param["digest"]=signObj.getwaySign;
    return super.post(param);
  }
  get(param){

  }
}
export default LoginModel;



