<template>
  <!-- 文章描述 -->
  <describeBox :content="getDescribeText" />
</template>
<script>
  import { mapState } from 'vuex'
  import describeBox from '@/components/describe.vue'
  export default {
    name: "articleDescribe",
    props:{
      componentKey:{
        type: Number,
        default: 0
      }
    },
    components:{
      describeBox
    },
    computed:{
      ...mapState({
        'articleDetail': state => state.articleState.data
      }),
      getDescribeText(){
        let DescribeText = '';
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='rich_text'&& item.sortKey==this.componentKey){
            DescribeText = decodeURIComponent(item.data.text);
          }
        })
        return DescribeText;
      },
      haveLine(){
        let data = true;
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='rich_text'&& item.sortKey==this.componentKey){
            data = item.haveLine;
          }
        })
        return data;
      }
    },
    methods:{
    }
  }
</script>

<style lang="scss" scoped>
  .article-describe-box{
    margin:0 25px;
    padding-bottom: 20px;
    color: #363C54;
    font-size: 14px;
    line-height: 25px;
    letter-spacing:2px;
    overflow: hidden;
  }

</style>



