<template>
  <transition name="slide-fade">
    <div v-show="isShow" v-if="detail.product" class="specifications">
      <div class="specifications_box" @click.self="$emit('closeSelect')">
        <div class="specifications_goods_info">
          <div class="goods_info_box" @click="preview">
            <img :src="skuPic.thumbnailURL" alt="">
            <span class="mask"></span>
          </div>
          <div class="goods_info_text">
            <div class="icon_box">
              <span v-show="detail.product.payType=='credit'||detail.product.payType=='all'" class="integral">{{selectedSku.price.currentUserPricePoint}}</span>
              <span v-show="detail.product.payType=='cash'||detail.product.payType=='all'" class="cash">&yen;{{selectedSku.price.currentUserPriceCash | precisionTwo}}</span>
            </div>
            <p>{{selectedSku.skuTitle}} {{ this.pageStatus.skuSpec}}</p>
          </div>
          <span class="icon_close" @click="$emit('closeSelect')"></span>
        </div>
        <div class="specifications_scroll" :style="{paddingBottom:isIphoneX?'98px':'74px'}">
          <!-- 1230 不显示付款方式 -->
          <div v-if="false" class="payment_select" style="margin-top:0;">
            <p class="select_title">付款方式:</p>
            <ul class="payment_top_ul">
              <!-- 积分 -->
              <li @click="changePayModel(payModel.credit)" v-show="detail.product.payType=='credit'||detail.product.payType=='all'">
                <div class="box" :class="payModel.credit.status">
                  <span  class="integral">{{this.selectedSku.price.currentUserPricePoint}}</span>
                  <span v-if="!creditPrice" class="integral_not_enough">积分不足</span>
                </div>
              </li>
              <!-- 混合 -->
              <li @click="changePayModel(payModel.mix)" v-if="mixPrice" v-show="detail.product.payType=='all'">
                <div class="box" :class="payModel.mix.status">
                  <span class="integral">{{this.userCredits}} + &yen;{{needCash() | precisionTwo}}</span>
                </div>
              </li>
              <!-- 现金 -->
              <li @click="changePayModel(payModel.money)" v-if="cashPrice" v-show="detail.product.payType=='cash'||detail.product.payType=='all'">
                <div class="box" :class="payModel.money.status" >
                  <span> &yen;{{selectedSku.price.currentUserPriceCash | precisionTwo}} </span>
                </div>
              </li>
            </ul>
          </div>
          <!-- <div class="color_select">
              <p class="select_title">颜色选择：金桔黄</p>
              <ul class="color_select_ul">
                  <li style="border-color:#00BCBC;"><span style=" background-color:#FAC261;"></span></li>
                  <li style="border:none;"><span style=" background-color:#040B29;"></span></li>
                  <li style="border:none;"><span style=" background-color:#CCD4D8;"></span></li>
              </ul>
          </div> -->
          <div class="payment_select color_select" v-for="(prop,pindex) in serialAttributes" :key="pindex">
            <!-- <p class="select_title">{{prop.attributeName}}选择：{{getSelectAttrStr(prop.attributeCode)}}</p> -->
            <p class="select_title">{{prop.attributeName}}选择：</p>

            <ul class="color_select_ul" v-if="prop.attributeValueType==2">

              <li v-for="(l,cindex) in prop.spuAttributeValueList"  :key="cindex"  :style="l.imageStatus" @click="changeAttr(l,prop,true)">
                <span :style="{backgroundImage: `url(${l.attributeValuePath})`}"></span>
              </li>
            </ul>
            <ul class="payment_ul" v-else>
              <li  v-for="(l, cindex) in prop.spuAttributeValueList" :key="cindex" :class="l.status" :id="l.attributeId"  @click="changeAttr(l,prop,true)">
                <span>{{l.attributeValue}}</span>
              </li>
            </ul>

          </div>
          <div class="color_select bottom">
            <p class="select_title" v-if="detail.product.spuLimitedAmount!='-999'">购买数量：限购{{detail.product.spuLimitedAmount}}件</p>
            <p class="select_title" v-if="detail.product.spuLimitedAmount=='-999'">购买数量：</p>
            <div class="number_box">
              <button @click="amountControl('-')" class="subtract" :class="{disabled:this.pageStatus.amount<=1}"></button>
              <span>{{this.pageStatus.amount}}</span>
              <button @click="amountControl('+')" class="add" :class="{disabled:addDisabled}"></button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  const attrCls={
    disable:"disabled",
    selected:"selected",
    normal:"",
    imageSelect:"border-color:#00BCBC;",
    imageNormal:"border-color:#696D7F;",
    imageDisable:"border-color:#cdced4;"
  };

  const payType={
    cash:"cash",
    mix:"mix",
    credit:"credit"
  }
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import { getCookie } from '@/supreme-core'
  import {mapState,mapMutations} from 'vuex'
  export default {
    name: "specifications",
    props:{
     isShow:{
        type: Boolean,
        default: false
     },
      hubus:{
        type:Object
      },
    },
    mounted(){
      this.procAttrStatus();
      this.loadDefaultSelect();
      //this.calculateAttrValidStatus();

    },
    computed:{
      ...mapState({
        'detail': state => state.detailState.data,
        'userCredits': state => state.detailState.userCredits,
        'serialAttributes':state => state.detailState.data.product.serialAttributes,
        'pageStatus': state => state.detailState.pageStatus,
        'payModel':state => state.detailState.pageStatus.payModel,
        'selectedSku': state => state.detailState.data.product.selectedSku
      }),
      isIphoneX(){
        return util.isIphoneX();
      },
      addDisabled(){
        // 加号变灰
        if(this.detail.product.spuLimitedAmount!='-999'&&this.pageStatus.amount>=this.detail.product.spuLimitedAmount){
          return true;
        }else if(this.selectedSku.skuLimitedAmount!='-999'&&this.pageStatus.amount>=this.selectedSku.skuLimitedAmount||this.pageStatus.amount>=this.selectedSku.remain||this.selectedSku.roleVo.saleStatus==0){
          return true;
        }else{
          return false;
        }
      },
      skuPic(){
        let skuPic={};
        this.detail.product.skuList.forEach(item=>{
          if(item.skuCode==this.selectedSku.skuCode){
            skuPic= item.skuPic[0];
          }
        })
        return skuPic;
      },
      creditPrice(){
        var result = this.userCreditst>=this.pageStatus.amount*parseInt(this.selectedSku.price.currentUserPricePoint) ;
        if(result){
          this.payModel.credit.status=attrCls.selected;
          this.payModel.money.status=attrCls.normal;
        }else{
          //this.payModel.mix.status=attrCls.normal;
          //this.payModel.money.status=attrCls.normal;
          this.payModel.credit.status=attrCls.disable;
        }
        return result;
      },
      mixPrice(){
        var result = this.userCredits<parseInt(this.selectedSku.price.currentUserPricePoint) && parseInt(this.userCredits) >0;
        if(result){
          this.payModel.mix.status=attrCls.selected;
          this.payModel.credit.status=attrCls.disable;
          this.payModel.money.status=attrCls.normal;
        }
        return result;
      },
      cashPrice(){
        if(this.creditPrice==false&&this.mixPrice==false){
          this.payModel.money.status=attrCls.selected;
        }else if(this.payModel.money.status==attrCls.selected){
          this.payModel.money.status=attrCls.selected;
          if(this.payModel.mix.status==attrCls.selected){
            this.payModel.mix.status=attrCls.normal
          }
          if(this.payModel.credit.status==attrCls.selected){
            this.payModel.credit.status=attrCls.normal
          }
        }
        return true;
      }


    },
    methods:{
      ...mapMutations({
        updateAttrSelectStatus:"detailState/updateAttrSelectStatus",
      }),
      preview(){
        // 规格图片放大
        hybrid.showImagesPreview({
          "images":[this.skuPic.enlargedURL],
          "index":0
        })
      },
      // 计算现金差额
      needCash(){
        var cash =parseFloat(this.selectedSku.price.currentUserPriceCash);
        // *this.pageStatus.amount;
        var point =parseFloat(this.userCredits)/10;
        return Math.round((cash-point)*100)/100;
      },
      /**
       * @修改付款模式
       * @param model 需要修改的paymodel
       * */
      changePayModel(model){
        if(model.status==attrCls.disable){
          return ;
        }
        if(model.status==attrCls.normal){
          for(var s in this.payModel){
            if(this.payModel[s].status==attrCls.selected){
              this.payModel[s].status=attrCls.normal;
            }
          }
          model.status=attrCls.selected;
        }
      },
      /**
       * @desc 数量控制
       * @params type + -
       * */
      amountControl(type){
        if(type=="+"){
          if(this.selectedSku.isSoldOut || !this.selectedSku.available || this.selectedSku.roleVo.saleStatus==0|| this.pageStatus.addToast){//下架|售罄|不可售
             return;
          }
          // spu限制，库存限制
          if(this.detail.product.spuLimitedAmount!='-999'&&this.pageStatus.amount>=this.detail.product.spuLimitedAmount){
            this.pageStatus.addToast = true;
            if(util.isInWebview()){
              hybrid.showToast({content:"单品已达上限"});
            }
            return;
          }else if(this.selectedSku.skuLimitedAmount!='-999'&&this.pageStatus.amount>=this.selectedSku.skuLimitedAmount){ // sku限制
            this.pageStatus.addToast = true;
            if(util.isInWebview()) {
              hybrid.showToast({content: "单品已达上限"});
            }
            return;
          }else if(this.pageStatus.amount>=this.selectedSku.remain){
            this.pageStatus.addToast = true;
            if(util.isInWebview()) {
              hybrid.showToast({content: "库存不足"});
            }
            return;
          }
          this.pageStatus.amount++;
        }else if(type=="-"){
          if(this.pageStatus.addToast){
            this.pageStatus.addToast = false;
          }
          if(this.pageStatus.amount>1){
            this.pageStatus.amount--;
          }
        }

      },
      // 加载默认sku 的属性到规格属性列表
      /**
       * params {
       * setSelect
       * reset
       * }
       * */
      procAttrStatus(params={}){
        this.detail.product.serialAttributes&&this.detail.product.serialAttributes.forEach((attrs)=>{
          attrs.spuAttributeValueList.forEach((attr,index)=>{
            if(this.getAttrStatus(attr)){
              attr.status=attrCls.normal;
              attr.imageStatus=attrCls.imageNormal;
            }else{
              attr.status=attrCls.disable;
              attr.imageStatus=attrCls.imageDisable;
            }
          })

        })
      },
      // 获取serialAttributes 中某一个attr的可用性，（是否出现在可以用sku中）
      getAttrStatus(attr){
        var hasIn=false;
        c:for(var sku of this.detail.product.skuList){
          d:for(var skuAttr of sku.seriesAttributes.skuAttributeValueOutputList){
            if(attr.valueCode == skuAttr.valueCode&&sku.remain>0){
              hasIn=true;
              break c;
            }
          }
        }
        return hasIn;
      },

      /**
       * @desc 获取通过spu 进入页面默认的sku
       * @return sku 返回默认的sku
       * */
      getDefaultSku(){
        var skuCode = this.selectedSku.skuCode;
        var skuResult;
        this.detail.product.skuList.forEach(function(sku,index){
          if(sku.skuCode==skuCode){
            skuResult=sku;
          }
        });
        return skuResult;
      },
      //
      /**
       * @desc 获取通过用户选择规格后得到的新的sku
       * @return sku 返回一个sku
       * */
      getSelectedSku(){
        var selectAttrs = this.getSelectAttrs();
        var resultSku ;
        this.detail.product.skuList.forEach(sku=>{

          if(sku.seriesAttributes&&sku.seriesAttributes.skuAttributeValueOutputList&&this.isInSkuAttrs(sku.seriesAttributes.skuAttributeValueOutputList,selectAttrs)){
            resultSku=sku;
          }
        });
        return resultSku;
      },
      //
      /**
       * @description 所选的属性是否某个sku attrs 中
       * @param skuAttr  标准的sku属性
       * @param selectAttrs 用户选择的属性
       * @return  true // false
       * */
      isInSkuAttrs(skuAttr,selectAttrs){

        var loop=0;
        var hasMultiPropProp=false;
        var attrbuteMap={
          length:0
        };
        for(var attr of skuAttr){
            if(attr.multiProp){
              hasMultiPropProp=true;
            }
            if(!attrbuteMap[attr.attributeCode]){
              attrbuteMap.length++;
              attrbuteMap[attr.attributeCode]=attr;
            }

        }
        if(!hasMultiPropProp&&selectAttrs.length!==skuAttr.length){
          return false;
        }
        for(var ska of skuAttr){
          var flag = false;
          for(var sa of selectAttrs ){
            if(sa.valueCode==ska.valueCode){
              flag=true;
            }
          }
          if(flag){
            loop++;
          }
        }
        if(hasMultiPropProp){
          return loop==attrbuteMap.length;
        }else{
          return loop==skuAttr.length;
        }

      },
      // 通过一个属性获取所在的属性组
      getAttrGroupByAttr(attr,serialAttributes){
        var group;
        c:for(var attrGourp of serialAttributes){
          for(var ar of attrGourp.spuAttributeValueList){
            if(ar.valueCode==attr.valueCode){
              group=attrGourp;
              break c;
            }
          }
        }
        return group;
      },
      //
      /**
       * @desc 获取用户已选择的属性
       * @return array 返回用户选择的规格数组
       * */
      getSelectAttrs(){
        var selectAttrs=[];
        this.detail.product.serialAttributes&&this.detail.product.serialAttributes.forEach((attrs)=>{
          attrs.spuAttributeValueList.forEach((attr,index)=>{
            if(attrs.attributeValueType!=2&&attr.status==attrCls.selected){
              attr.attributeCode=attrs.attributeCode;
              attr.multiProp=attrs.multiProp;
              selectAttrs.push(attr);
            }
            if(attrs.attributeValueType==2&&attr.imageStatus==attrCls.imageSelect){
              attr.attributeCode=attrs.attributeCode;
              attr.multiProp=attrs.multiProp;
              selectAttrs.push(attr);
            }
          })

        })
        return selectAttrs;
      },
      /**
       * @desc 获取已选择属性的文本
       * @return str
       * */
      getSelectAttrStr(attributeCode){
        var str="";
        this.detail.product.serialAttributes&&this.detail.product.serialAttributes.forEach((attrs)=>{
          if(attributeCode==attrs.attributeCode){
            attrs.spuAttributeValueList.forEach((attr,index)=>{
              if(attrs.attributeValueType!=2&& attr.status==attrCls.selected){
                str=attr.attributeValue;
              }
              if(attrs.attributeValueType==2&& attr.imageStatus==attrCls.imageSelect){
                str=attr.attributeValue;
              }
            })
          }
        })
        return str;
      },
      //
      /**
       * @desc 更新规格选择组合后的sku名称
       *
       * */
      updateSkuSpecAndPropName(){
        var selectAttrs = this.getSelectAttrs();
          var str=""
          selectAttrs.forEach(attr=>{
            str+= attr.attributeValue;
            str+="  ";
          });
          this.pageStatus.skuSpec=str;
      },
      /*
        * @desc 计算产品规格的可选状态,核心功能**********************
        * */
      calculateAttrValidStatus(attr){
        var arrObj={}
        // 通过重复取可用的属性的交集来确定不可用的属性
        this.detail.product.skuList.forEach(sku=> {
          sku.seriesAttributes && sku.seriesAttributes.skuAttributeValueOutputList && sku.seriesAttributes.skuAttributeValueOutputList.forEach(atr => {
            // 首先把所有对应的属性组的属性全部放进去，通过下一步的反向过滤多余数据
            if(attr.attributeCode==atr.attributeCode){
              // 当前选择的属性组
              var ax = this.getAttrInAttrGroup(atr.attributeCode,atr.valueCode);
              // 除去当前状态不能选择的情况
              if(ax.status==attrCls.disable||ax.imageStatus==attrCls.imageDisable){
                return ;
              }

              if(!arrObj[atr.attributeCode]){
                arrObj[atr.attributeCode]=[];
              }
              if(attr.multiProp){
                var selectedAttrs = this.getSelectAttrs();
                if(this.canPartMatchSku(attr,selectedAttrs)){
                  arrObj[attr.attributeCode].push(attr)
                }
              }else{

                if(!this.isInExistAttr(atr,arrObj[atr.attributeCode])){
                  arrObj[atr.attributeCode].push(atr)
                }
              }

            }
            if(attr.valueCode==atr.valueCode){
              // 当前选择的属性组
              sku.seriesAttributes&& sku.seriesAttributes.skuAttributeValueOutputList.forEach(ar=>{
                var ax = this.getAttrInAttrGroup(ar.attributeCode,ar.valueCode);
                // 除去当前状态不能选择的情况
                if(ax.status==attrCls.disable||ax.imageStatus==attrCls.imageDisable){
                  return ;
                }
                if(!arrObj[ar.attributeCode]){
                  arrObj[ar.attributeCode]=[];
                }
                if(!this.isInExistAttr(ar,arrObj[ar.attributeCode])){
                  // 如果属性是multiProp 特殊处理
                  if(ar.multiProp){
                    var selectedAttrs = this.getSelectAttrs();
                    if(this.canPartMatchSku(ar,selectedAttrs)){
                      arrObj[ar.attributeCode].push(ar)
                    }
                  }else{
                    arrObj[ar.attributeCode].push(ar)
                  }
                }

              });
            }

          });

        })
        this.setInvalidAttr(arrObj);
      },

      /**
       * @desc 设置属性的状态 （设置无效）
       * */
      setInvalidAttr(arrObj){
        this.detail.product.serialAttributes&&this.detail.product.serialAttributes.forEach(attrs=>{
          attrs.spuAttributeValueList.forEach(at=>{
            var isValid=false;
            arrObj[attrs.attributeCode]&&arrObj[attrs.attributeCode].forEach(a=>{
              if(at.valueCode==a.valueCode){
                isValid=true;
              }
            })
            if(!isValid){
              if(attrs.attributeValueType==2){
                at.imageStatus=attrCls.imageDisable;
              }else{
                at.status=attrCls.disable;
              }

            }else{
              if(at.status!=attrCls.selected&&at.imageStatus!=attrCls.imageSelect){
                if(attrs.attributeValueType==2){
                  at.imageStatus=attrCls.imageNormal;
                }else{
                  at.status=attrCls.normal;
                }
              }
            }

          })

        });
      },
      // 是否可以部分匹配到sku（针对属性选中一半的情况）
      // 该方法专门处理 multiProp 下的情况
      canPartMatchSku(ar,selectedAttrs){
        var sku =this.getSelectedSku();
        var matchSkus=[];
        var isMatch=false;
        if(!sku){
          for(var i=0;i<selectedAttrs.length;i++){
            if(selectedAttrs[i].multiProp){
              selectedAttrs.splice(i,1);
              i--;
            }
          }
          c:for(var sku of this.detail.product.skuList){
            if(sku.seriesAttributes && sku.seriesAttributes.skuAttributeValueOutputList){
              var count = 0;
              d:for(var atr of  sku.seriesAttributes.skuAttributeValueOutputList){
                e:for(var sAttr of selectedAttrs){
                  if(atr.valueCode==sAttr.valueCode){
                    count++;
                  }
                }
              }
              if(count==selectedAttrs.length){
                matchSkus.push(sku);
              }
            }
          }
          for(var msku of matchSkus){
            for(var atr of  msku.seriesAttributes.skuAttributeValueOutputList){
              if(ar.valueCode==atr.valueCode){
                isMatch=true;
              }
            }
          }
        }else{
          for(var atr of  sku.seriesAttributes.skuAttributeValueOutputList){
            if(ar.valueCode==atr.valueCode){
              isMatch=true;
            }
          }
        }
        return isMatch;
      },
      // 处理其他同级的属性
      procOtherAttr(attributeCode){
        var hasSelected=false;
        var sku = this.getDefaultSku();
        this.detail.product.serialAttributes&&this.detail.product.serialAttributes.forEach((attrs)=>{
          if(attributeCode==attrs.attributeCode){
            attrs.spuAttributeValueList.forEach((attr,index)=>{
              if(attr.status==attrCls.status||attr.imageStatus==attrCls.imageSelect){
                hasSelected=true;
              }
              if(attrs.attributeValueType==2){
                if(attr.imageStatus!=attrCls.imageDisable){
                  attr.imageStatus=attrCls.imageNormal;
                }
              }else{
                if(attr.status!=attrCls.disable) {
                  attr.status = attrCls.normal;
                }
              }
            })
          }
        })
        return hasSelected;
      },
      // 获取属性组中的属性
      getAttrInAttrGroup(attributeCode,valueCode){
        var resultAttr;
        a:for(var attrGroup of this.detail.product.serialAttributes){
          if(attributeCode==attrGroup.attributeCode){
             b:for(var attr of attrGroup.spuAttributeValueList){
               if(attr.valueCode==valueCode){
                 resultAttr=attr;
                 break;
               }
             }
          }
        }
        return resultAttr;
      },

      isInExistAttr(attr,array){
        var flag=false;
        for(var a of array){
          if(attr.valueCode==a.valueCode){
            flag=true;
            break;
          }
        }
        return flag;
      },
      // 加载默认选中项
      loadDefaultSelect(){
        var sku = this.getDefaultSku();
        // 处理isMulit 的情况，让多选的默认选中第一个
        var hasSelectedMap={};
        /**
        this.detail.product.serialAttributes&&this.detail.product.serialAttributes.forEach((attrs)=>{
          attrs.spuAttributeValueList.forEach((attr,index)=>{
            sku.seriesAttributes&&sku.seriesAttributes.skuAttributeValueOutputList.forEach(skuAttr=>{
              if(attr.valueCode==skuAttr.valueCode){
                var group = this.getAttrGroupByAttr(attr,this.serialAttributes);
                console.log(attr.attributeValue);
                this.changeAttr(attr,group,false);
              }
            });
          })
        })
        */
        if( sku&& sku.seriesAttributes){
          c:for(var skuAttr of  sku.seriesAttributes.skuAttributeValueOutputList){
            if(this.detail.product.serialAttributes){
              d:for(var attrs of this.detail.product.serialAttributes){
                for(var attr of attrs.spuAttributeValueList){
                  if(skuAttr.valueCode==attr.valueCode){
                    var group = this.getAttrGroupByAttr(attr,this.serialAttributes);
                    if(!hasSelectedMap[skuAttr.attributeCode]){
                      this.changeAttr(attr,group,false);
                    }
                    hasSelectedMap[skuAttr.attributeCode]=true;
                    break d;
                  }
                }
              }
            }
          }
        }

      },
      /**
       * @description 用户选择属性后的处理
       * @param l 属性
       * @param prop  属性所在的大分类属性
       * @params isLoad 是否加载sku 数据
       * */
      changeAttr(attr,attrGroup,isLoad){
        if(attr.status==attrCls.disable||attr.imageStatus==attrCls.imageDisable){
          return ;
        }

        if(attr.status==attrCls.selected&&attrGroup.attributeValueType!=2){ // 非图片类型的反选中
          attr.status=attrCls.normal;
        }else if(attr.imageStatus==attrCls.imageSelect&&attrGroup.attributeValueType==2){//普通类型的反选中
          attr.imageStatus=attrCls.imageNormal;
        } else if(attr.status==attrCls.normal&&attrGroup.attributeValueType!=2){ // 非图片类型的选中
          this.procOtherAttr(attrGroup.attributeCode);
          attr.status=attrCls.selected;
        }else if(attr.imageStatus==attrCls.imageNormal&&attrGroup.attributeValueType==2){ // 图片类型的选中
          this.procOtherAttr(attrGroup.attributeCode);
          attr.imageStatus=attrCls.imageSelect;
        }
        attr.attributeCode=attrGroup.attributeCode;
        var selectAttrs = this.getSelectAttrs();
        this.procAttrStatus({reset:true}); // 把所有图片状态还原
        for(var a of selectAttrs){
          var group = this.getAttrGroupByAttr(a,this.serialAttributes);
          if(group.attributeValueType==2){
            a.imageStatus=attrCls.imageSelect;
          }else{
            a.status=attrCls.selected;
          }
          this.calculateAttrValidStatus(a);
        }

        var sku = this.getSelectedSku();
        //if(selectAttrs.length)
        /**
        if(sku&&this.selectedSku.skuId==sku.skuId){
          return ;
        }
         */
        this.pageStatus.canBuy=false;
        this.pageStatus.canAddCart=false;
        if(sku){
          this.updateSkuSpecAndPropName();
          if(isLoad){
            this.$emit("saveQueryParams",{data:{skuCode:sku.skuCode,reload:true}});
            this.$emit("loadDetail",{data:{skuCode:sku.skuCode,reload:true}})
          }else{
            this.pageStatus.canBuy=true;
            this.pageStatus.canAddCart=true;
          }
        }else{
          this.pageStatus.skuSpec="";
        }

      }
    }
  }
</script>

