<template>
 <!-- 作者 -->
  <div class="author-box" :style="{borderBottom:haveLine&&'1px solid #F0F1F2'}">
    <div class="header" @click="gotoPersonalPage(getAuthor.accountId)">
      <img class="header_img" :src="getAuthor.headImage" alt="">
      <img v-show="getAuthor.identityImage" class="identity_icon" :src="getAuthor.identityImage" alt="v">
      <!-- <span class="icon"
        :class="[
            getAuthor.identity=='intentional_owner'||getAuthor.identity=='pre_owner'?'is_deposit':'',
            getAuthor.identity=='owner'||getAuthor.identity=='coowner'?'is_owner':'']"></span> -->
    </div>
    <div class="information">
      <p @click="gotoPersonalPage(getAuthor.accountId)">
        <span :class="{icon_employees:getAuthor.isNioAuthorized}">{{getAuthor.nickname}}</span>
        <!-- <span v-show="getAuthor.isNioAuthorized" class="icon_employees"></span> -->
      </p>
      <span class="tag">{{getAuthor.userTags}}</span>
    </div>
    <span class="follow_but" v-show="isToken && getAuthor.userRelation > -1" @click.stop="FollowBut(getAuthor)" :class="{but:getAuthor.userRelation==0||getAuthor.userRelation==3}">
      {{getAuthor.userRelation==0&&'关注'||getAuthor.userRelation>3 &&'关注'|| getAuthor.userRelation==1&&'已关注' || getAuthor.userRelation==3&&'聊天'}}
    </span>
  </div>
</template>
<script>
  import { mapState, mapMutations } from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import {supreme, getCookie,type} from '@/supreme-core'
  import { FollowModel} from '@/model'
  export default {
    name: "Author",
    data(){
      return{
      
      }
    },
    computed:{
      ...mapState({
        'articleDetail': state => state.articleState.data,
      }),
      getAuthor(){
        let authorData = {};
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='people_setting_tab'){
            authorData = item.data;
          }
        })
        return authorData;
      },
      haveLine(){
        let data = true;
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='people_setting_tab'){
            data = item.haveLine;
          }
        })
        return data;
      },
      isToken(){
        return util.isToken();
      }
    },
    methods:{
      ...mapMutations({
        browserOpenNIO:"detailState/browserOpenNIO",
      }),
      gotoPersonalPage(userid){ 
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          // 人设头像 点击 埋点
          hybrid.buriedPointClick({
            eventKey:'MallPage_Content_User_Click',
            currentPage:'MallPage_',
            NextPage:'',
            params:{
              content_id:this.getQueryString('pageCode'),
              targartaccountid:this.getAuthor.accountId
            }
          })
          // 跳转用户
          hybrid.openPage({type:1,shema:`nio://friend/info?userID=${userid}`});
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      FollowBut(item){
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          // 人设关注 点击 埋点
          hybrid.buriedPointClick({
            eventKey:'MallPage_Content_UserFollow_Click',
            currentPage:'MallPage_',
            NextPage:'',
            params:{
              content_id:this.getQueryString('pageCode'),
              targartaccountid:this.getAuthor.accountId
            }
          })
          switch(parseInt(item.userRelation)){
            case -1:
                console.log('不能关注自己');
                return;
                break;
            case 0:
                // console.log('发起关注请求！')
                this.followFun(item)
                break;
            case 1:
                console.log('已关注');
                return;
                break;
            case 3:
                hybrid.openPage({type:1,shema:`nio://conversation?id=${item.imUser.imId}&name=${encodeURI(item.imUser.nick)}&type=private`})
                console.log('互相关注,跳转到聊天');
                return;
                break;
            case 4:
                console.log('不可取消关注，存在于好友列表');
                return;
                break;
            case 5:
                console.log('不可关注，存在于最近联系人中');
                return;
                break;
            case 6:
                console.log('不可关注，群成员中可能出现');
                return;
                break;
          }
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      followFun(userItem){
          // 只有 0 才会发起关注接口
        if(util.isInWebview()){
          let params = {
              data:{
                  type:"follow",
                  accountId:userItem.accountId
              }
          }
          if(type.isString(params["data"])){
            try{
                params["data"]=JSON.parse(params["data"]);
            }catch (ex){
                throw ex;
                return ;
            }
          }
          FollowModel.execute(params).then(data=>{
              let {resultDesc, resultData} = data.data;
              if(resultDesc == 'success'){
                userItem.userRelation = resultData;
                hybrid.showToast({content: "关注成功"});
              }
          }).catch(err=>{
              console.log(err)
          });
        }else{
            this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
        
      }
    }
  }
</script>

<style lang="scss" scoped>
  .author-box{
    position: relative;
    margin: 0 25px 20px;
    padding: 20px 0;
    // border-top: 1px solid #F0F1F2;
    // border-bottom: 1px solid #F0F1F2;
    display: flex;
    align-items: center;
    .header{
      position: relative;
      height: 40px;
      width: 40px;   
      display: flex;
      justify-content: center;
      align-items: center;   
      border-radius: 20px;
      margin-right: 8px;
      background-color: #F7F7F8;
      // overflow: hidden;
      .header_img{
        width: 40px;
        height: 40px;
        object-fit: cover;
        border-radius: 20px;
        vertical-align: middle;
      }
      .identity_icon{
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 11;
        width: 14px;
        height: 14px;
      }
      // .is_owner{
      //   background: url('../../assets/svg/icon_nio_vip.svg') center no-repeat;
      //   background-size: cover;
      // }
      // .is_deposit{               
      //   background: url('../../assets/svg/icon_deposit_vip.svg') center no-repeat;
      //   background-size: cover;
      // }
    }
    .information{
      display: flex;
      flex-direction: column;
      p{
        margin-right: 78px;
        display: flex;
        font-size: 16px;
        line-height: 22px;
        color: #363C54;
        span{
          overflow: hidden;
          text-overflow: ellipsis;
          display:-webkit-box;
          /*! autoprefixer: off */
          -webkit-box-orient: vertical;
          /* autoprefixer: on */
          -webkit-line-clamp:1;
        }
        .icon_employees{
          padding-right: 26px;
          height: 20px;
          background: url('../../assets/svg/icon_employees.svg') right center no-repeat;
          background-size: 20px;
        }
      }
      .tag{
        color: #9B9DA9;
        font-size: 14px;
        line-height: 20px;
      }
    }
    .follow_but{
      position: absolute;
      right: 0;
      width: 65px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      color: #CDCED4;
      font-size: 12px;
      background-color: #f7f7f8;
      border-radius: 15px;
      &.but{
        color: #00BCBC;
        background-color: #E6FAFA;
      }
    }
  }
</style>



