export default {
  data() {
    return {
      show: false
    }
  },
  methods: {
    cancel(params){
      this.toggle(false)
    },
    confirm(params){
      this.toggle(false)
      if(this.confirmHandle){
        this.confirmHandle(params)
      }
    },
    toggle(bool){
      this.show = bool
    }
  }
}



