import {BaseStore} from '@/supreme-core';
import {
  getCommentsModel} from '@/model'
let state = {
  pageStatus:{
  },
  data: {

  },
  // 精选评论
  selectCommentsData:{
   
  },
  // 最新评论
  newCommentsData:{
   
  },
}

let mutations = {
  //初始化数据
  setDetail(state,data){
    //supreme.$.extend()
    state.data = data
  },
  selectComments(state,data){
    state.selectCommentsData = data
  },
  newComments(state,data){
    state.newCommentsData = data
  },
  moreNewComments(state, data){
    data.comments = [
      ...state.newCommentsData.comments,
      ...data.comments
    ];
    state.newCommentsData = data;
  },
  deleteCommentsLi(state, data){
    let newCommentsData = state.newCommentsData.comments.filter((item) => item.commentId !== data);
    let selectCommentsData = state.selectCommentsData.comments.filter((item) => item.commentId !== data);
    state.data.commentCount--;
    state.newCommentsData.comments = newCommentsData;
    state.selectCommentsData.comments = selectCommentsData;
  },
  addCommentsLi(state, data){
    let newDatas = state.newCommentsData.comments = [
      data,
      ...state.newCommentsData.comments
    ];
    state.data.commentCount++;
    state.newCommentsData.comments = newDatas;
  },
  addReplytoTopLi(state, data){
    // 添加 最新回复
    state.newCommentsData.comments.forEach(itemLi => {
      if(itemLi.commentId==data.id){
        itemLi.replies = [
          data.replies,
          ...itemLi.replies
        ];
        itemLi.replyCount++;
      }
    });
    // 添加 精选回复
    state.selectCommentsData.comments.forEach(itemLi => {
      if(itemLi.commentId==data.id){
        itemLi.replies = [
          data.replies,
          ...itemLi.replies
        ];
        itemLi.replyCount++;
      }
    });
  }
}

let actions = {
  /**
   * @access public
   * @desc  获取评论列表
   * @offset 评论偏移数
   * @type 评论类型 1精选、2最新
   * */
  getCommentsList({ commit, state }, {offset, type} = { offset: 0, type: 2 }){//获取评论
    getCommentsModel.execute({data:{offset:offset, count:10, swcArticleId:state.data.swcArticleId,type:type}}).then(data=>{
      let { resultData } = data.data;
      if(type==2){
        // 设置 最新评论
        
        if(offset>0){//最新评论 分页
          commit('moreNewComments', resultData);
        }else{
          commit('newComments', resultData);
        }
      }else{
        // 设置 精选评论
        commit('selectComments', resultData);
      }
    })
  },
}

var store= BaseStore.getInstance({
  state: state,
  mutations: mutations,
  actions: actions
});
export default {
  namespaced: true,
  state: state,
  mutations: mutations,
  actions: actions
}



