// 基础类库，提供页面基本功能通过mixin 方式插入到该方法
/**
 * @description 设置一个页面滚动遮罩，防止误触
 * @author xinliang.lin
 * @date 2019/1/16
 * */

export default{
  data(){
    return{
      canAutoScroll:false
    }
  },
  created(){},
  updated(){

  },
  mounted(){
    this.initDom();
  },
  deactivated(){},
  activated(){},
  methods:{
    initDom(){
      // 初始化
      let bannedClick = document.getElementsByClassName('bannedClick')[0];
      let divmask = document.createElement('div'); 
      divmask.setAttribute('class', 'banned-click-mask');
      bannedClick.appendChild(divmask);
      // 监听滚动
      bannedClick.onscroll = function(event){
        if(!bannedClick.getElementsByClassName('banned-click-mask').length>0){
          bannedClick.appendChild(divmask);
        }
        divmask.setAttribute('style', `height:${bannedClick.scrollHeight}px;width:100%;position: absolute;left: 0;top: 0;z-index:100;`);
        if(this.scrollInterval == null){// 未发起时，启动定时器，1秒1执行  
          this.scrollInterval = setInterval(()=>{
            if(bannedClick.scrollTop == this.bannedClickScrollTop) {  
              bannedClick.removeChild(divmask);
              clearInterval(this.scrollInterval);  
              this.scrollInterval = null;  
            }  
          }, 1000);  
        }
        this.bannedClickScrollTop = event.target.scrollTop;
      }
      // 阻止点击事件
      bannedClick.onclick = function(event){
        event.stopPropagation();
      }
    }
  }
}



