<template>
  <div class="seriesPage">
    <component 
      v-for="item in seriesState.seriesList" 
      :key="item.sortKey" 
      :componentKey="item.sortKey"
      v-bind:is="currentTabComponent(item)"></component>
    <!-- banner -->
    <!-- <SeriesBanner /> -->    
    <!-- 描述 -->
    <!-- <SeriesDescribe /> -->
    <!-- 单列商品-->
    <!-- <GoodsSingle /> -->
    <!-- 双列商品 -->
    <!-- <GoodsDouble /> -->
    <!-- <div class="series-nomore">
      <span class="line"></span>
      <span>没有更多了</span>
      <span class="line"></span>
    </div> -->

  </div>
</template>

<script>
  import {supreme,baseMixin} from '@/supreme-core'
  import { mapState, mapMutations } from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import headerNav from '@/components/header-nav.vue'
  import SeriesBanner from './SeriesBanner.vue'
  import SeriesDescribe from './SeriesDescribe.vue'
  import GoodsDouble from './GoodsDouble.vue'
  import GoodsSingle from './GoodsSingle.vue'  
  import SeriesMore from './SeriesMore.vue'  
  import {seriesModel} from '@/model'
  export default {
    name: "seriesPage",
    mixins: [baseMixin],
    computed: {
      ...mapState({
        'seriesState': state => state.seriesState.data,
      }),
    },
    components:{
      headerNav,
      SeriesBanner,
      SeriesDescribe,
      GoodsDouble,
      GoodsSingle,
      SeriesMore
    },
    created(){
       let params = {
          data:{
            pageCode:this.getQueryString('pageCode')
          }
        }
        seriesModel.execute(params).then(data=>{
          let { resultDesc, resultData ,message} = data.data;
          if(resultDesc=='success'){
            this.setSeriesData(resultData);
            // set微信分享
            if(resultData.share.shareTitle){
              initWXShare.share({
                title: resultData.share.shareTitle,
                desc: resultData.share.shareDesc,
                link:`${util.getEnvInfo().openUrl}/series?pageCode=${supreme.getQueryString("pageCode")}&share=true`,
                imgUrl: resultData.share.shareImgUrl,
              });
            }
          }else{
            this.goto('/error',{message: message, resultDesc:resultDesc});
            return;
          }
        });
    },
    methods:{
      ...mapMutations({
        setSeriesData:"seriesState/setSeriesData",
      }),
      currentTabComponent(newValue){
        switch(newValue.type){
          case 'banner':
            return 'SeriesBanner';
            break;
          case 'richText':
            return 'SeriesDescribe';
            break;
          case 'bigItem':
            return 'GoodsSingle';
            break;
          case 'gridGoodsItem':
            return 'GoodsDouble';
            break;
          case 'lastText':
            return 'SeriesMore';
            break;

        }
      },
      backToLastPage(){
        if(util.isInWebview()){
          // 关闭当前页面
          hybrid.close();
        }else{
          this.back();
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
.seriesPage{
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: #fff;
}
</style>



