import supreme from './supreme'
import hybridFn from './hybrid/hybridFn'
import BaseModel from './model/BaseModel'
import BaseStore from './store/BaseStore'
import taskMgr from './task/taskMgr'
import {setCookie,getCookie,guid,validators,getQueryString,type,getTimestamp,copy} from './util/util'
import  {baseMixin,
  gestureBackMixin,
  scrollBehaviorMixin,
  singleSwitchMixin,
  scrollBannedClickMixin,
  gestureMixin} from './reuse/mixin'
/**
 *@exports index
 *@desc 该文件是supreme-core 框架导出所有的对外统一访问的module function 和 class，不建议使用非对外公布的module 和class
 *@author lei.you
 *@date 2018/3/20
 * */
export {


    /**
     * @property {module }
     * @desc module supreme 框架的核心模块，初始化程序的时候加载
     * */
    supreme,
      /**
       * @property {module }
       * @desc module hybridFn hybrid 交互模块 ，用来通过bridge 调用native 方法
       * */
    hybridFn,
      /**
       * @property {class }
       * @desc class 数据交互的基类，规范model 的写法和调用方式
       *
       * */
    BaseModel,
      /**
       *  @property {class }
       * @desc  状态管理的基类 增强型的状态管理用法
       * */

    BaseStore,
    /**
     *  @property {module }
     * @desc  task 管理器，对task stack 封装操作
     * */
    taskMgr,

    /**
       @property {function}
       @desc setCookie
       */
    setCookie,
    /**
     @property {function}
     @desc setCookie
     */
    getCookie,
    /**
     @property {module}
     @desc guid
     */
    guid,
    /**
     @property {module}
     @desc validators
     */
    validators,
    /**
     @property {function}
     @desc getQueryString
     */
    getQueryString,
    /**
     @property {module}
     @desc type
     */
    type,
    /**
     @property {function}
     @desc getTimestamp
     */
    getTimestamp,
    /**
     @property {module}
     @desc copy
     */
    copy,

    /**
     * @property {module}
     * @desc 提供页面基本功能通过mixin 方式插入到该方法,需要用在vue 页面的引导页中，一般就是index.vue 页面，可以通过mixins 或者extends 的方式被组件使用
     * 使用了该组件页面就具有了taskStack 自管理，页面的事件总线，网络状态的判断等功能 @module baseMixin*/
    baseMixin,
    /**
     * @property {module}
     * @desc 手势回退 @module gestureBackMixin */
    gestureBackMixin,
    /**
     * @property {module}
     * @desc 滚动条处理 @module scrollBehaviorMixin */
    scrollBehaviorMixin,
    /**
     * @property {module}
     * @desc singleSwitch @module singleSwitchMixin */
    singleSwitchMixin,
    scrollBannedClickMixin,
    /**
     * @property {module}
     * @desc 手势模块@module gestureMixin */
    gestureMixin

};



