<template>
  <div class="banner-box">
    <img @click="showBanner(getBanner)" v-lazy="getBanner.articleBigPicUrl" alt="" />
  </div>
</template>
<script>
  import { mapState, mapMutations } from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import {supreme,getCookie} from '@/supreme-core'
  export default {
    name: "ArticleBanner",
    data(){
      return{
      
      }
    },
    computed:{
      ...mapState({
        'articleDetail': state => state.articleState.data,
      }),
      getBanner(){
        let banner = '';
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='banner'){
            banner = item.data[0];
          }
        })
        return banner;
      }
    },
    methods:{
      showBanner(url){
        if(util.isInWebview()){
          // 页面返回按钮埋点
          hybrid.buriedPointClick({
            eventKey:'MallContentPage_Image_Click',
            currentPage:'MallContentPage',
            NextPage:'',
            params:{
              content_id:this.getQueryString('pageCode'),
              url:url
            }
          })
          console.log('显示头图');
         
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .banner-box{
    display:flex;
    align-items:center; 
    justify-content:center;
    width: 100%;
    height: 3.75rem;
    background-color: #F7F7F8;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    img[lazy=loading] {
      width: 40px;
      height: 40px;
    }   
  }
</style>



