<template>
  <div class="series-nomore">
    <span class="line"></span>
    <span>{{getMore.text}}</span>
    <span class="line"></span>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  export default {
    name: "SeriesMore",
    props:['componentKey'],
    computed:{
      ...mapState({
        'seriesState': state => state.seriesState.data,
      }),
      getMore(){
        let text = '';
        this.seriesState.seriesList.forEach(item => {
          if(item.type=='lastText'&&item.sortKey==this.componentKey){
            text = item.data;
          }
        });
        return text;
      }
    },
    methods:{
    }
  }
</script>

<style lang="scss" scoped>
.series-nomore{
  margin: 60px 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: #CCD4D8;
  line-height: 22px;
  .line{
    width: 20px;
    margin: 0 10px;
    border-top: 1px solid #CCD4D8;
  }
}
</style>



