<template>
  <!-- 公共导航 -->
  <header @click.stop="doubleClick" class="header_nav" :style="{background:headerStyle,height:isIphoneX?'88px':'64px'}">
    <span class="icon-back2" @click.stop="backBut"></span>
    <span class="title"><h3>{{title}}</h3></span>
    <span v-show="!shareShow">
      <!-- 占位标签 -->
    </span>
    <span v-show="shareShow" class="icon-share2" @click.stop="shareBut"></span>
  </header>
</template>
<script>
  import { getCookie } from '@/supreme-core';
  import util from '@/util/util'
  export default {
    name: "header-nav",
    data(){
      return{ 
        doubleNum:0,
        isDisable:false
      }
    },
    props: {
      shareShow: {
          type: Boolean,
          //text必须
          default: true
      },
      title:{
        type:String,
        default:''
      },
      headerStyle:{
        type: String,
        default:'rgba(255, 255, 255, 1)'
      }
    },
    computed:{
      isIphoneX(){
        return util.isIphoneX();
      }
    },
    mounted(){
    
    },
    methods:{
      backBut(){
        if(!this.isDisable){
          this.$emit('back');
          this.isDisable = true;
        }
        setTimeout(() => {
          this.isDisable = false;
        }, 1000)
      },
      shareBut(){
        this.$emit('share')
      },
      doubleClick(){
        let timer;
        this.doubleNum++;
        if(this.doubleNum>=2){
          clearTimeout(timer);
          this.doubleNum = 0;
          this.$emit('double')
        }else {
          timer = setTimeout(()=>{
            this.doubleNum = 0;
          },1000)
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
    .header_nav{
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 82px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end;
      padding-left:5px;
      padding-right: 13px;
      background:linear-gradient(0deg,rgba(0,0,0,0),rgba(0,0,0,0));
      z-index: 12;
      span{
        padding: 12px;
        box-sizing:content-box;
      }
      // .icon-back{
      //   width: .24rem;
      //   height: .24rem;
      //   background:url("../../assets/svg/icon_back.svg") center no-repeat;
      //   background-size: .28rem;
      // }
      .icon-back2{
        width: 24px;
        height: 24px;
        background:url("../assets/svg/icon_back2.svg") center no-repeat;
        background-size: 24px;
      }
      // .icon-share{
      //   width: .24rem;
      //   height: .24rem;
      //   background:url("../../assets/svg/icon_share.svg") center no-repeat;
      //   background-size: .28rem;
      // }
      .title{
        padding: 0;
        font-size: 17px;
        color: #040B29;
      }
      .icon-share2{
        width: 24px;
        height: 24px;
        background:url("../assets/svg/icon_share2.svg") center no-repeat;
        background-size: 24px;
      }
    }
</style>



