import Mock from 'mockjs'
import detail from './detail'
import article from './article'
import series from './series'

//Mock.mock(/doLogin/,'post',doLogin);
Mock.mock(/detail/,"post",function(){
  return detail;
});

Mock.mock(/article/,"post",function(){
  return article;
});

Mock.mock(/series/,"post",function(){
  return series;
});





