<template>
  <!-- 推荐文章 -->
  <div>
    <!-- 精选评论 -->
    <div class="article-comments" v-show="selectCommentsData.comments&&selectCommentsData.comments.length>0">
      <p class="title">精选评论</p>
      <ul class="comments-list">
        <li v-for="(item, index) in selectCommentsData.comments" :key="index">
          <div class="comments-list-top" @click="gotoCommentsDetails(item)">
            <div class="header" @click.stop="gotoPersonalPage(item.profile.accountId)">
              <img class="header_img" :src="item.profile.headImage" alt="">
              <img v-show="item.profile.identityImage" class="icon" :src="item.profile.identityImage" alt="">
              <!-- <span class="icon" 
                :class="[
                  item.profile.identity=='intentional_owner'||item.profile.identity=='pre_owner'?'is_deposit':'',
                  item.profile.identity=='owner'||item.profile.identity=='coowner'?'is_owner':'']"></span> -->
            </div>
            <div class="name-box" @click.stop="gotoPersonalPage(item.profile.accountId)">
              <p :class="{icon_employees:item.profile.isNioAuthorized}">{{item.profile.nickname}}</p>
              <span>{{item.createTime | formatDate}}</span>
            </div>
            <span class="like" @click.stop="addArticleCommentLiked(item)" :class="{cur:item.hasLiked}">{{item.likeCount}}</span>
          </div>
          <div class="comments-list-text">
            <optionsTab @clickTab="selectAction" :optionsText="getOptionsText(item)">
              <p @click.stop="replyComments(item.commentId, 2, item.profile.nickname)">{{item.comment}}</p>
            </optionsTab>
            <div class="reply" v-show="item.replies.length>0">
              <p v-show="replyIndex<2" @click.stop="replyComments(item.commentId, 3, replyData.name, replyData.replyId)" v-for="(replyData, replyIndex) in item.replies" :key="replyIndex">
                <i @click.stop="gotoPersonalPage(replyData.userId)"> {{replyData.name}}</i>
                <span> 回复 </span>
                <i @click.stop="gotoPersonalPage(replyData.replyToUserId)">{{replyData.replyToName}}</i>
                <span>:{{replyData.comment}}</span>
              </p>
              <p class="look-all" v-show="item.replyCount>2" @click="gotoCommentsDetails(item)">- 查看全部回复 {{item.replyCount}} 条</p>
            </div>
          </div>
        </li>
      </ul>
      <!-- <div class="comments-look-more">
        <span>查看更多评论 </span>
      </div> -->
    </div>
    <!-- 最新评论(可加载更多) -->
    <div class="article-comments"  :style="{borderBottom:haveLine&&'1px solid #F0F1F2'}" v-show="newCommentsData.comments&&newCommentsData.comments.length>0">
      <p class="title">最新评论</p>
      <ul class="comments-list">
        <li v-for="(item, index) in newCommentsData.comments" :key="index">
          <div class="comments-list-top" @click="gotoCommentsDetails(item)">
            <div class="header" @click.stop="gotoPersonalPage(item.profile.accountId)" >
              <img class="header_img" :src="item.profile.headImage" alt="">
              <img v-show="item.profile.identityImage" class="icon" :src="item.profile.identityImage" alt="">
              <!-- <span class="icon" 
                :class="[
                    item.profile.identity=='intentional_owner'||item.profile.identity=='pre_owner'?'is_deposit':'',
                    item.profile.identity=='owner'||item.profile.identity=='coowner'?'is_owner':'']"></span> -->
            </div>
            <div class="name-box" @click.stop="gotoPersonalPage(item.profile.accountId)">
              <p :class="{icon_employees:item.profile.isNioAuthorized}">{{item.profile.nickname}}</p>
              <span>{{item.createTime | formatDate}}</span>
            </div>
            <span class="like" @click.stop="addArticleCommentLiked(item)" :class="{cur:item.hasLiked}">{{item.likeCount}}</span>
          </div>
          <div class="comments-list-text">
            <optionsTab @clickTab="selectAction" :optionsText="getOptionsText(item)">
              <p @click.stop="replyComments(item.commentId, 2, item.profile.nickname)">{{item.comment}}</p>
            </optionsTab>
            <div class="reply" v-show="item.replies.length>0">
              <p v-show="replyIndex<2" @click.stop="replyComments(item.commentId, 3, replyData.name, replyData.replyId)" v-for="(replyData, replyIndex) in item.replies" :key="replyIndex">
                <i @click.stop="gotoPersonalPage(replyData.userId)"> {{replyData.name}}</i>
                <span> 回复 </span>
                <i @click.stop="gotoPersonalPage(replyData.replyToUserId)">{{replyData.replyToName}}</i>
                <span>:{{replyData.comment}}</span>
              </p>
              <p class="look-all" v-show="item.replyCount>2" @click="gotoCommentsDetails(item)">- 查看全部回复 {{item.replyCount}} 条</p>
            </div>
          </div>
        </li>
      </ul>
      <div v-show="newCommentsData.hasMore" @click="loadingComments" class="comments-look-more">
        <span>查看更多评论 </span>
      </div>
    </div>
  </div>
</template>
<script>
  import { mapState, mapMutations, mapActions } from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import {supreme,getCookie} from '@/supreme-core'
  import {
    addReplytoModel,
    commentLikedModel,
    getCommentsModel,
    deleteCommentModel,
    reportCommentModel
  } from '@/model'
  import optionsTab from '@/components/options-tab.vue'
  export default {
    name: "CommentsList",
    data(){
      return{
        pageNum:0
      }
    },
    filters: {
      formatDate:util.formatDate
    },
    components:{
      optionsTab
    },
    computed:{
      ...mapState({
        'selectCommentsData': state => state.articleState.selectCommentsData,
        'newCommentsData': state => state.articleState.newCommentsData,
        'articleDetail': state => state.articleState.data
      }),
      // getCommentsList(){
      //   let listItem = [];

      //   this.articleDetail.articleComponent.find(item=>{
      //     if(item.type == 'comment'){
      //       listItem = item.data;
      //     }
      //   })
      //   return listItem;
      // }
      haveLine(){
        let data = true;
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='comment'){
            data = item.haveLine;
          }
        })
        return data;
      }
    },
    created(){
      // 精选评论get
      this.getCommentsList({offset:0,type:1});
      // 最新评论get
      this.getCommentsList();
    },
    methods:{
      ...mapMutations({
        deleteCommentsLi:"articleState/deleteCommentsLi",
        browserOpenNIO:"detailState/browserOpenNIO"
      }),
      ...mapActions({
        getCommentsList: 'articleState/getCommentsList' // 将 `this.add()` 映射为 `this.$store.dispatch('increment')`
      }),
      getOptionsText(item){
        return [
          {...item, type:1, text:'回复'},
          {...item, type:2, text:'复制'},
          {...item, type:item.buttonType==1&&3||item.buttonType==2&&4,
            text:item.buttonType==1&&'删除'||item.buttonType==2&&'举报'
          }
        ];
      },
      loadingComments(){
        this.pageNum++
        this.getCommentsList({offset:this.pageNum,type:2})
        // if(util.isInWebview()){
        //   if(!util.isToken()){
        //     hybrid.openLoginPage();
        //     return;
        //   }
        //   if(util.newVersion()){
        //     hybrid.openPage({type:1,shema:`nio://mall/article/comment?id=${this.articleDetail.swcArticleId}`})
        //     return;
        //   }
        //   hybrid.openPage({type:1,shema:`nio://content/comment_list?id=${this.articleDetail.swcArticleId}&content_type=content`})
        // }
      },
      selectAction(item){
        switch(item.type){
          case 1://回复当前评论
            this.replyComments(item.commentId, 2, item.profile.nickname);
            break;
          case 2://复制功能
            if(util.newVersion()){
              dsBridge.call("common.getCopyText",item.comment,()=>{});
            }else{
              hybrid.copyText({msg:item.comment},function(data){
                if(data.data.copyResult){
                  hybrid.showToast({content: "复制成功"});
                }
              });
            }
            break;
          case 3://删除(自己的评论)
            this.deleteComment(item);
            break;
          case 4://举报(别人的评论)
            this.reportComment(item);
            break;
        }
      },
      gotoPersonalPage(accountId){
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          if(accountId=='' || accountId==null)return;
          // 跳转用户
          hybrid.openPage({type:1,shema:`nio://friend/info?userID=${accountId}`});
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      gotoCommentsDetails(item){
        let self = this;
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          // 跳转评论详情
          if(util.newVersion()){
            dsBridge.call("NIODoBridge.openURL", `nio://mall/user.comment.detail?commentId=${item.commentId}&id=${this.articleDetail.swcArticleId}`);
            // dsBridge.call("NIODoBridge.openURL", `nio://content?id=${this.articleDetail.swcArticleId}&comment_id=${item.commentId}&content_type=content`);
            return;
          }
          hybrid.jumpArticleCommentsDetail({
            commentId: item.commentId,
            id: this.articleDetail.swcArticleId,
            contentType: "article",
            commentTitle:""
          },data=>{
            console.log(data);
            // 从评论详情返回 待优化
            this.getCommentsList({offset:0,type:1});
            this.getCommentsList();
          })
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      replyComments(id, type, name, replyId=0){//回复评论
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          this.$emit('textareaFocus', {commentId: id, type:type, name:name, replyId:replyId});
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      addArticleCommentLiked(item){//点赞评论
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          commentLikedModel.execute({data:{commentId:item.commentId, isLiked:!item.hasLiked}}).then(data=>{
            let { resultDesc } = data.data;
            if(resultDesc=='success'){
              item.hasLiked = !item.hasLiked;
              if(item.hasLiked){
                item.likeCount++;
                // hybrid.showToast({content: "点赞成功"});
              }else{
                item.likeCount--;
                // hybrid.showToast({content: "取消点赞"});
              }
            }
          })
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      deleteComment(item){
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          deleteCommentModel.execute({data:{swcArticleId:this.articleDetail.swcArticleId, commentId:item.commentId}}).then(data=>{
            let { resultDesc } = data.data;
            if(resultDesc == 'success'){
              this.deleteCommentsLi(item.commentId);
              hybrid.showToast({content: "删除成功"});
            }else{
              hybrid.showToast({content: "删除失败"});
            }
          });
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      reportComment(item){
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          reportCommentModel.execute({data:{commentId:item.commentId}}).then(data=>{
            let { resultDesc } = data.data;
              if(resultDesc == 'success'){
                hybrid.showToast({content: "举报成功"});
              }else{
                hybrid.showToast({content: "举报失败"});
              }
          });
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      isToken(){
        if(getCookie('userToken') && getCookie('userToken') != null){
          return true;
        }else{
          return false;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .article-comments{
    margin:0 25px;
    padding-top: 20px;
    // border-bottom: 1px solid #F0F1F2;
    .title{
      color: #9B9DA9;
      font-size: 16px;
      line-height: 22px;
    }
  }
  .comments-list{
    margin-top:20px;
    li{
      display: flex;
      flex-direction: column;
      margin-bottom: 24px;
    }
    li:last-child{
      .comments-list-text{
        border: none;
        padding-bottom: 0;
      }
    }
  }
  .comments-list-top{
    position: relative;
    display: flex;
    align-items: center;
    .header{
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 40px;
      width: 40px;
      // border-radius: 20px;
      // background-color: #F7F7F8;
      // overflow: hidden;
      .header_img{
        height: 40px;
        width: 40px;
        object-fit: cover;
        border-radius: 20px; 
        vertical-align: middle;
      }
      .icon{
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 11;
        width: 14px;
        height: 14px;
        // border-radius: 7px;
      }
      .is_owner{
        background: url('../../assets/svg/icon_nio_vip.svg') center no-repeat;
        background-size: cover;
      }
      .is_deposit{               
        background: url('../../assets/svg/icon_deposit_vip.svg') center no-repeat;
        background-size: cover;
      }
    }
    .name-box{
      margin-left: 10px;
      display: flex;
      flex-direction: column;
      p{
        font-size: 16px;
        line-height: 20px;
        color: #363C54;
        margin-right: 54px;
        overflow: hidden;
        word-break:break-all; 
        text-overflow: ellipsis;
        display:-webkit-box;
        /*! autoprefixer: off */
        -webkit-box-orient: vertical;
        /* autoprefixer: on */
        -webkit-line-clamp:1;
        &.icon_employees{
          padding-right: 26px;
          height: 20px;
          background: url('../../assets/svg/icon_employees.svg') right center no-repeat;
          background-size: 20px;
        }
      }
      span{
        color: #9B9DA9;
        font-size: 12px;
        line-height: 20px;
      }
    }
    .like{
      position: absolute;
      right: 0;
      top: 0;
      font-weight: 300;
      color: #9B9DA9;
      font-size: 14px;
      line-height: 18px;
      padding-right: 30px;
      padding-top:6px;
      background: url('../../assets/svg/icon_praise.svg') right center no-repeat;
      background-size: 20px;
      &.cur{
        background: url('../../assets/svg/icon_praise_select.svg') right center no-repeat;
        background-size: 20px;
      }
    }
  }
  .comments-list-text{
    margin-top: 10px;
    margin-left: 50px;
    font-size: 14px;
    border-bottom: 1px solid #F0F1F2;
    padding-bottom: 20px;
    p{
      line-height: 20px;
      word-wrap:break-word
    }
    .reply{
      margin-top: 10px;
      padding:10px 10px 0;
      border-radius: 2px;
      background-color: #F7F7F8;
      p{
        padding-bottom: 10px;
        color: #00BCBC;
      }
      .look-all{
        font-size:12px;
      }
      i{
        font-style: normal;
      }
      span{
        color: #696D7F;
      }
    }
  }
  .comments-look-more{
    margin: 0 auto;
    width: 120px;
    height: 30px;
    border-radius: 14px;
    font-size: 14px;
    text-align: center;
    line-height: 30px;
    color: #00BCBC;
    background-color: #F0FCFC;
  }
</style>



