<template>
  <div class="article-box">
    <headerNav v-show="articleDetail.articleComponent&&isHeaderShow" :shareShow="true" :headerStyle="headerStyle" @back="backToLastPage" @share="share" />
    <section class="content-box bannedClick" ref="content" v-on:scroll.passive="pageScroll" >
      <component 
        v-for="item in articleDetail.articleComponent" 
        :key="item.sortKey" 
        :componentKey="item.sortKey"
        @textareaFocus="getFocus" 
        v-bind:is="currentTabComponent(item)"></component>
      <!-- banner -->
      <!-- <ArticleBanner v-if="item.type=='single_picture'"  /> -->
      <!-- 标题 -->
      <!-- <ArticleTitle v-if="item.type=='title'"  /> -->
      <!-- 作者 -->
      <!-- <Author v-if="item.type=='people_setting_tab'"  /> -->
      <!-- 文章描述（带商品） -->
      <!-- <articleDescribe v-if="item.type=='text_goods'" /> -->
      <!-- 推荐文章 -->
      <!-- <recommendArticle v-if="item.type=='article_recommendation'" /> -->
      <!-- 评论列表 -->
      <!-- <CommentsList v-if="item.type=='comment'" /> -->
    </section>
    <footer v-show="articleDetail.articleComponent" class="article-comments-input" :style="{paddingBottom:isIphoneXBotton?'36px':'8px'}">
      <div @click="textareaClickFocus" class="article-textarea-box" :style="{marginRight:!textareaObj.focus?'10px':'0'}">
        <pre class="article-textarea-pre">{{textareaObj.value}}</pre>
        <textarea ref="textareaID" class="article-textarea" @blur="textareaBlur"  @focus="textareaFocus" 
          v-model="textareaObj.value" :placeholder="textareaObj.type==1?textareaObj.placeholder:'回复 '+textareaObj.placeholder+':'"></textarea>
        <span v-show="textNumberEnough" class="article-textarea-number" :class="{redclass:textNumberOut}">{{textNumberGreater}}</span>
      </div>     
      <div class="comments-icon">
        <span v-show="!textareaObj.focus&&textareaObj.value==''" @click="gotoComments()" class="icon-review">{{articleDetail.commentCount}}</span>
        <span v-show="!textareaObj.focus&&textareaObj.value==''" @click="articleLiked()" class="icon-like" :class="{select:articleDetail.hasLiked}">{{articleDetail.likeCount}}</span>
        <span v-show="textareaObj.focus || textareaObj.value.length>0" @click="sendBut" class="send-text">发送</span>
      </div>
    </footer>
    <!-- 输入框遮罩 -->
    <div v-show="textareaObj.focus" class="textarea-mask"></div>
  </div>
</template>
<script>
  import { mapState, mapMutations, mapActions } from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import initWXShare from '@/util/initWXShare'
  import {supreme,getCookie,baseMixin, scrollBehaviorMixin, scrollBannedClickMixin } from '@/supreme-core'
  import ArticleBanner from './ArticleBanner.vue'
  import ArticleTitle from './ArticleTitle.vue'
  import ArticleDescribe from './ArticleDescribe.vue'
  import ArticleGoods from './ArticleGoods.vue'
  import RecommendArticle from './RecommendArticle.vue'
  import CommentsList from './CommentsList.vue'
  import Author from './Author.vue'
  import headerNav from '@/components/header-nav.vue'
  import {
    ArticleModel,
    reportModel, 
    addCommentModel, 
    articleLikedModel,
    addReplytoModel,
    creditShareModel} from '@/model'
  export default {
    name: "articlePage",
    data(){
      return{
        scrollTopNum:0,
        headerStyle:'',
        durationTime:0,
        textareaObj:{
          value:'',
          placeholder:'我也来说～',
          focus:false,
          type:1,//回复类型,1为评论文章、2回复评论、3回复返回
          replyId:'',
          commentId:''
        },
      }
    },
    components:{
      ArticleBanner,
      ArticleTitle,
      Author,
      ArticleDescribe,
      ArticleGoods,
      RecommendArticle,
      CommentsList,
      headerNav
    },
    mixins: [baseMixin,scrollBehaviorMixin,scrollBannedClickMixin],
    watch: {
      scrollTopNum(newval, oldVal){
        if(newval>10){
          this.headerStyle=`rgba(255, 255, 255, ${newval/400})`;

        }else if(newval>400){
          this.headerStyle='rgba(255, 255, 255, 1)';

        }else{
          this.headerStyle='linear-gradient(0deg,rgba(0,0,0,0),rgba(0,0,0,0))';
        }
      }
    },
    computed:{
      ...mapState({
        'articleDetail': state => state.articleState.data,
      }),
      getAuthorId(){
        let accountId = '';
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type == 'people_setting_tab'){
              accountId = item.data.accountId
          }
        })
        return accountId;
      },
      isIphoneX(){
        return util.isIphoneX();
      },
      isIphoneXBotton(){
        if(this.isIphoneX&&!this.textareaObj.focus){
          return true;
        }else if(this.isIphoneX&&!this.textareaObj.focus&&this.textareaObj.value.length>0){
          return true;
        }
        return false;
      },
      textNumberEnough(){//输入文字大于180
        if(this.textareaObj.value.length>180){
          return true;
        }
        return false;
      },
      textNumberGreater(){//还可输入多少文字
        return 200-this.textareaObj.value.length;
      },
      textNumberOut(){//输入文字大于200个字
        if(this.textareaObj.value.length>200)return true;
        return false;
      },
      isHeaderShow(){
        return util.isInWebview();
      },
      getTitleText(){
        let titleData = {};
        this.articleDetail&&this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='title'){
            titleData = item;
          }
        })
        return titleData;
      }
    },
    appear(data){
       // 精选评论get
      this.getCommentsList({offset:0,type:1});
      // 最新评论get
      this.getCommentsList();
    },
    created(){
      // 取消弹性效果
      util.isInWebview()&&hybrid.disableWebviewPull();
      // if(window.plus){
      //     this.plusReady();
      // }else{
      //     document.addEventListener("plusready",this.plusReady,false);
      // }
      this.getArticleData();
    },
    mounted(){
      this.duration = setInterval(()=>{
        this.durationTime++;
      },1000);
    },
    destroyed(){
      clearInterval(this.duration);
    },
    methods:{
      ...mapMutations({
        setDetail:"articleState/setDetail",
        addCommentsLi:"articleState/addCommentsLi",
        addReplytoTopLi:"articleState/addReplytoTopLi",
        browserOpenNIO:"detailState/browserOpenNIO",
        // setAutoScroll: "articleState/setAutoScroll"
      }),
      ...mapActions({
        getCommentsList: 'articleState/getCommentsList' // 将 `this.add()` 映射为 `this.$store.dispatch('increment')`
      }),
      pageScroll(event){
        this.scrollTopNum = event.target.scrollTop;
      },
      //
      getFocus(data){
        // console.log(data)
        this.textareaObj.type = data.type;
        this.textareaObj.commentId = data.commentId;
        this.textareaObj.placeholder =  data.name;
        this.textareaObj.replyId =  data.replyId;
        this.$refs.textareaID.focus();
      },
      textareaClickFocus(){
        if(util.isInWebview()){
          if(!util.isToken())return;
          this.$refs.textareaID.focus();
        }
      },
      resetTextarea(){
        this.textareaObj.focus = false;
        this.textareaObj.value = '';
        this.textareaObj.type = 1;
        this.textareaObj.placeholder = '我也来说～';
        this.$refs.textareaID.blur();
      },
      currentTabComponent(newValue){
        switch(newValue.type){
          case 'banner':
            return 'ArticleBanner';
            break;
          case 'title':
            return 'ArticleTitle';
            break;
          case 'people_setting_tab':
            return 'Author';
            break;
          case 'rich_text':
            return 'ArticleDescribe';
            break;
          case 'single_goods':
            return 'ArticleGoods';
            break;
          case 'article_recommendation':
            return 'RecommendArticle';
            break;
          case 'comment':
            return 'CommentsList';
            break;
        }
      },
      // web获取设备信息
      plusReady(){
          alert( "IMEI: " + plus.device.imei );
      },
      //如果应用是应hybrid 形式回退的名字需要是固定backToLastPage
      backToLastPage(){
        if(util.isInWebview()){
          // 页面返回按钮埋点
          hybrid.buriedPointClick({
            eventKey:'MallContentPage_Back_Click',
            currentPage:'MallContentPage',
            NextPage:'',
            params:{
              content_id:this.getQueryString('pageCode')
            }
          })
          // 页面离开埋点
          hybrid.trackEndPage();
          // 关闭当前页面
          hybrid.close();
        }
      },
      textareaBlur(){
        // 失焦，无输入重置
        if(this.textareaObj.value == '' && this.textareaObj.value.length==0){
          this.textareaObj.value = '';
          this.textareaObj.type = 1;
          this.textareaObj.placeholder = '我也来说～';
        }
        this.textareaObj.focus = false;
      },
      textareaFocus(){
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            this.$refs.textareaID.blur();
            return;
          }
          this.textareaObj.focus = true;
        }
      },
      getArticleData(){
        let params = {
          data:{
            pageCode:this.getQueryString('pageCode')
          }
        }
        ArticleModel.execute(params).then(data=>{
          let { resultDesc, resultData} = data.data;
          if(resultDesc=='success'){
            this.setDetail(resultData);
            // 上报浏览
            this.reportReading();
            // set微信分享
            if(this.articleDetail.shareTitle){
              initWXShare.share({
                title: this.articleDetail.shareTitle,
                desc: this.articleDetail.shareDesc,
                link:`${util.getEnvInfo().openUrl}/article?pageCode=${supreme.getQueryString("pageCode")}&share=true`,
                imgUrl:this.articleDetail.shareImgUrl,
              });
            }
          }else{
            this.goto('/error',{message: data.data.message, resultDesc:data.data.resultDesc});
            return;
          }
        });
      },
      reportReading(){
        reportModel.execute(this.setReportParams()).then(data=>{
          // console.log(data)

        });
      },
      sendBut(){//发表文章评论
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          if(!this.articleDetail.enabledComment){
            hybrid.showToast({content: "您已被禁言"});
            return;
          }else if(this.textareaObj.value.length>200){
            hybrid.showToast({content: "所输入字数超过限制，请删减修改后再发送"});
            return;
          }else if(this.textareaObj.value.length==''){
            hybrid.showToast({content: "请输入评论内容..."});
            return;
          }
          switch(this.textareaObj.type){
            case 1://发表评论
              this.addArticleComment();
              break;
            case 2://回复评论
              this.addReplyto();
              break;
            case 3://回复-回复
              this.addReplyto();
              break;
          }
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      /*
      * 发表文章评论
      */
      addArticleComment(){
         addCommentModel.execute({data:{swcArticleId:this.articleDetail.swcArticleId, comment:this.textareaObj.value}}).then(data=>{
           let { resultDesc, resultData } = data.data;
           if(resultDesc == 'success'){
              this.addCommentsLi(resultData);              
              this.resetTextarea();
           }
          })
        
      },
      /*
      * 发表回复 或 回复评论的回复
      */
      addReplyto(){
        let addData = {//设置默认回复刷新数据
          comment:this.textareaObj.value,
          enabledModify:false,
          name:"",
          replyId:"",
          replyToName:this.textareaObj.placeholder,
          replyToUserId:"",
          userId:""
        }

        addReplytoModel.execute({data:{
          swcArticleId:this.articleDetail.swcArticleId, 
          commentId:this.textareaObj.type==3?this.textareaObj.replyId:this.textareaObj.commentId,
          comment:this.textareaObj.value
          }}).then(data=>{
          let { resultDesc, resultData } = data.data;
          if(resultDesc == 'success'){
            addData.name = resultData.profile.nickname;
            addData.userId = resultData.profile.accountId;
            addData.replyId = resultData.commentId;
            // 回复数据更新
            this.addReplytoTopLi({id:this.textareaObj.commentId,replies:addData})
            this.resetTextarea();
           }
        })
      },

      articleLiked(){//点赞文章
        if(util.isInWebview()){
          // 判断Token 是否登录
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          articleLikedModel.execute({data:{swcArticleId:this.articleDetail.swcArticleId,isLiked:!this.articleDetail.hasLiked}}).then(data=>{
            let { resultDesc } = data.data;
            if(resultDesc == 'success'){
              this.articleDetail.hasLiked = !this.articleDetail.hasLiked;
              if(this.articleDetail.hasLiked){
                this.articleDetail.likeCount++
              }else{
                this.articleDetail.likeCount--
              }
            }
          })
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      gotoComments(){
        if(util.isInWebview()){
          if(!util.isToken()){
            hybrid.openLoginPage();
            return;
          }
          if(util.newVersion()){
            hybrid.openPage({type:1,shema:`nio://mall/article/comment?id=${this.articleDetail.swcArticleId}`});
            return;
          }
          hybrid.openPage({type:1,shema:`nio://content/comment_list?id=${this.articleDetail.swcArticleId}&content_type=content`})
        }
      },
      share(){
        if(util.isInWebview()){
          if(util.newVersion()){
            let sharePlat = '',
                shareUrl = `${util.getEnvInfo().openUrl}/article?pageCode=${supreme.getQueryString("pageCode")}&share=true`;

            dsBridge.call("NIODoBridge.share", {
              title: this.articleDetail.shareTitle,
              subtitle: this.articleDetail.shareDesc,
              imgUrl: this.articleDetail.shareImgUrl,
              urlScheme: shareUrl,
            },(sharedata)=>{
              if(sharedata.result=='fail'){
                hybrid.showToast({content:"分享失败"})
                return;
              }else if(sharedata.result=='cancel'){
                hybrid.showToast({content:"分享取消"})
                return;
              }
              // 分享渠道
              if(sharedata.plat=='niofriend'){
                hybrid.showToast({content:"分享成功"})
                return;
              }else if(sharedata.plat=='weibo'){
                sharePlat = 'weibo';
              }else if(sharedata.plat=='wechatfriend'||sharedata.plat=='wechatcircle'){
                sharePlat = 'weixin';
              }
              // weixin/weibo 成功发起请求
              creditShareModel.post({data:{
                type:'share_news',
                data:{
                  news_id:this.articleDetail.swcArticleId,
                  news_title:this.getTitleText.data.title,
                  channel:sharePlat
                }
              }}).then((result)=>{
                let {resultDesc, resultData} = result.data;
                if(resultDesc!='success')return;
                if(resultData.credit>0){
                  hybrid.showToast({content:`分享成功，+${resultData.credit}积分`})
                }else{
                  hybrid.showToast({content:'分享成功'})
                }
              })
            });
            return;
          }
          hybrid.share(
            {
              "type":"share_news",//商品分享传"share_goods",文章分享传"share_news"
              "id": this.articleDetail.swcArticleId,
              "imageUrl": this.articleDetail.shareImgUrl,
              "name": this.articleDetail.shareTitle,
              "score":"403",//商品价格
              "shareUrl":location.href+"&share=true",
              "shareDesc":
                {
                  "title": this.articleDetail.shareTitle,
                  "description": this.articleDetail.shareDesc
                }
            },function(){

            });
        }
      },
      //设置上报参数  
      setReportParams(){
        if(util.newVersion()){
          let getOSInfo = supreme.localCache.get("getOSInfo"),
              userInfo = supreme.localCache.get('userInfo'),
              appInfo = dsBridge.call("common.getAppInfo", "")

          return{
            // accessToken:getCookie('userToken'),
            data:{
              brand:getOSInfo['device_brand'],//手机品牌
              model:getOSInfo['device_model'],//手机型号
              channel:getOSInfo['channel'],//android市场渠道号
              os:getOSInfo['device_type'],//操作系统类型，取值：android/ios/web
              os_lang:getOSInfo['os_lang'],//客户端的 原始语言值，若未知, 写 unknown 
              os_timezone:getOSInfo['os_timezone'],//客户端的 原始时区值，若未知, 写 unknown
              client_timestamp:Math.round(new Date() / 1000),//客户端的unix timestamp, 以秒为单位
              os_ver:getOSInfo['version'],//操作系统版本
              app_ver:appInfo['version'],//app版本，比如：1.0.0
              network:getOSInfo['network_type'],//联网方式，取值 2g/3g/4g/wifi/unknwon
              device_id:getOSInfo['device_id'],//设备id，IOS使用idfa，anroid使用android_id
              user_id:userInfo['account_id'],//登录用户从login接口得到的id
              events:[{
                "timestamp" : Math.round(new Date() / 1000), 
                "event_type" : "view_content", 
                "source" : "app",//app/weibo/wechat
                "source_user_id": this.getAuthorId,//分享用户id，仅H5页面需要该字段
                "duration" : this.durationTime,//停留时间，以秒为单位
                "content_id" : this.articleDetail.swcArticleId//内容id
              }]
            }
          }
        }
        let appInfo = JSON.parse(getCookie('appInfo'));
        if(appInfo!=null && util.isInWebview()){
          return{
            accessToken:getCookie('userToken'),
            data:{
              brand:appInfo['mobileinfo']['device_model'],//手机品牌
              model:appInfo['mobileinfo']['device_brand'],//手机型号
              channel:appInfo['mobileinfo']['channel'],//android市场渠道号
              os:appInfo['mobileinfo']['device_type'],//操作系统类型，取值：android/ios/web
              os_lang:appInfo['mobileinfo']['os_lang'],//客户端的 原始语言值，若未知, 写 unknown 
              os_timezone:appInfo['mobileinfo']['os_timezone'],//客户端的 原始时区值，若未知, 写 unknown
              client_timestamp:Math.round(new Date() / 1000),//客户端的unix timestamp, 以秒为单位
              os_ver:appInfo['systemVersion'],//操作系统版本
              app_ver:appInfo['mobileinfo']['app_version'],//app版本，比如：1.0.0
              network:appInfo['mobileinfo']['network_type'],//联网方式，取值 2g/3g/4g/wifi/unknwon
              device_id:appInfo['mobileinfo']['device_id'],//设备id，IOS使用idfa，anroid使用android_id
              user_id:appInfo['mobileinfo']['userId'],//登录用户从login接口得到的id
              events:[{
                "timestamp" : Math.round(new Date() / 1000), 
                "event_type" : "view_content", 
                "source" : "app",//app/weibo/wechat
                "source_user_id": this.getAuthorId,//分享用户id，仅H5页面需要该字段
                "duration" : this.durationTime,//停留时间，以秒为单位
                "content_id" : this.articleDetail.swcArticleId//内容id
              }]
            }
          }
        }
        let webContent = "unknwon";
        let ua = navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == 'micromessenger') { // 微信浏览器判断
            webContent = "wechat";
        } else if (ua.match(/WeiBo/i) == "weibo") {
            webContent = "weibo";
        }  
        return{
          data:{
            brand:"unknown",
            model:'unknown',
            channel:'unknown',
            os:'web',
            os_lang:navigator.language,
            os_timezone:'unknown',
            client_timestamp:Math.round(new Date() / 1000),
            os_ver:'unknown',
            app_ver:'unknown',
            network:'unknown',
            device_id:'unknown',
            user_id:'unknown',
            events:[{
              "timestamp" : Math.round(new Date() / 1000), 
              "event_type" : "view_content", 
              "source" : webContent,
              "source_user_id": this.getAuthorId,
              "duration" : this.durationTime,
              "content_id" : this.articleDetail.swcArticleId
            }]
          }
        }
  
      }
    }
  }
</script>

<style lang="scss" scoped>
