    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 110;
    .comments-icon{
      display: flex;
      height: 32px;
      align-items: center;
      align-self: flex-end;
      .send-text{
        height: 32px;
        color: #00BCBC;
        font-size: 14px;
        line-height: 32px;
        padding-left: 15px;
      }
      .icon-review{
        text-align: center;
        padding-top: 20px;
        min-width: 20px;
        background: url('../../assets/svg/icon_review.svg') top center no-repeat;
        background-size: 20px;
        color: #9B9DA9;
        font-size: 12px;
      }
      .icon-like{
        text-align: center;
        margin-left: 20px;
        padding-top: 20px;
        min-width: 20px;
        background: url('../../assets/svg/icon_praise.svg') top center no-repeat;
        background-size: 20px;
        color: #9B9DA9;
        font-size: 12px;
        &.select{
          background: url('../../assets/svg/icon_praise_select.svg') top center no-repeat;
          background-size: 20px;
        }
      }
    }
  }
  .article-textarea-box{
    position: relative;
    flex: 1;
    margin:0;
    padding:0;
    // line-height:normal;
    font-size: 16px;
    line-height: 24px;
    height: auto;
    min-height: 35px;
  }
  .article-textarea-pre{
    display: block;
    // word-wrap:break-word;
    word-break:break-all; 
    visibility: hidden;
    overflow: hidden;
    margin: 0;
    padding: 5px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-size: 16px;
    line-height: 24px;
    min-height: auto;
    max-height: 116px;
  }
  .article-textarea{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    margin: 0;
    padding:5px;
    // overflow-y:hidden;
    background-color:#F7F7F8;
    border: 0;
    // -webkit-user-select:none;
    word-break:break-all; 
    font-size: 16px;
    line-height: 24px;
    font-family:sans-serif;
    color:#2C3E50;
    &.no-border{
      outline: 0 none;
      border: none !important
    }
    &.no-resize{
      resize:none;
    }
  }
  .article-textarea-number{
    position: absolute;
    right: 10px;
    bottom: 0px;
    font-size: 12px;
    color: #9B9DA9;
    &.redclass{
      color: #EF7575;
    }
  }
  .textarea-mask{
    position: absolute;
    left: 0;
    top:0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 100;
  }
</style>



