<template>
  <!-- 推荐文章 -->
  <div class="recommend-article" :style="{borderBottom:haveLine&&'1px solid #F0F1F2'}">
    <h4 class="title">推荐文章</h4>
    <ul class="recommend-list">
      <li @click="gotoArticlePage(item)" v-for="(item, index) in getRecommendList" :key="index">
        <div class="banner">
          <img v-lazy="item.articleMiniPicUrl" alt="">
        </div>
        <div class="content-text">
          <p>{{item.title}}</p>
          <span>{{item.subtitle}}</span>
          <span class="browse">{{item.readCount}} 人浏览</span>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import { mapState, mapMutations } from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import {supreme,getCookie} from '@/supreme-core'
  export default {
    name: "RecommendArticle",
    data(){
      return{
      }
    },
    computed:{
      ...mapState({
        'articleDetail': state => state.articleState.data,
      }),
      getRecommendList(){
        let list = [];
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type == 'article_recommendation'){
            list = item.data;
          }
        })
        return list;
      },
      haveLine(){
        let data = true;
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='article_recommendation'){
            data = item.haveLine;
          }
        })
        return data;
      }
    },
    methods:{
      ...mapMutations({
        browserOpenNIO:"detailState/browserOpenNIO",
      }),
      gotoArticlePage(item){
        if(util.isInWebview()){
          // 推荐文章 点击 埋点
          hybrid.buriedPointClick({
            eventKey:'MallContentPage_PublishContent_Click',
            currentPage:'MallContentPage',
            NextPage:'',
            params:{
              content_id:this.getQueryString('pageCode'),
              targartaccountid:item.pageCode
            }
          })

          // 跳转文章详情
          hybrid.openPage({type:1,shema:`nio://mall/article?pageCode=${item.pageCode}`});
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .recommend-article{
    border-bottom: 1px solid #F0F1F2;
    margin: 0 25px;
    padding: 20px 0;
    overflow: hidden;
    .title{
      color: #040B29;
      font-size: 20px;
      line-height: 20px;
    }
  }
  .recommend-list{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    li{
      margin-bottom: 30px;
      display: flex;
      flex-direction: row;
      // &:last-child{
      //   margin-bottom: 0;
      // }
    }
    .banner{
      display:flex;
      align-items:center; 
      justify-content:center;
      width: 105px;
      height: 105px;
      background-color: #F7F7F8;
      border-radius: 2px;
      img{
        width: 105px;
        height: 105px;
        border-radius: 2px;
        object-fit: cover;
      }
      img[lazy=loading] {
        width: 40px;
        height: 40px;
      }  
    }
    .content-text{
      margin-left: 12px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      p{
        color: #040B29;
        font-size: 16px;
        line-height: 24px;
        height: 48px;
        margin-bottom: 5px;
        overflow: hidden;
        text-overflow: ellipsis;
        display:-webkit-box;
        /*! autoprefixer: off */
        -webkit-box-orient: vertical;
        /* autoprefixer: on */
        -webkit-line-clamp:2;
      }
      span{
        color: #9B9DA9;
        font-size: 14px;
        line-height: 20px;
        overflow: hidden;
        text-overflow: ellipsis;
        display:-webkit-box;
        /*! autoprefixer: off */
        -webkit-box-orient: vertical;
        /* autoprefixer: on */
        -webkit-line-clamp:1;
      }
      .browse{
        margin-top: 12px;
      }
    }
  }
</style>



