// 基础类库，提供页面基本功能通过mixin 方式插入到该方法
/**
 * @author lei.you
 * 保存页面滚动条位置，并恢复到到滚动位置
 * */
import $ from 'webpack-zepto';// 实在不想使用jquery操作 但是dom查询 和事件控制上jquery 太方便了

var doBind=function(){

}
export default{
  created(){
    this.pos={};
  },

  updated(){
    this._bindScroll()
  },
  mounted(){

    this._bindScroll()

  },
  deactivated(){

  },
  activated(){

    this.$route.meta&&this.$route.meta.keepAlive&&(()=>{
      if(!$.isEmptyObject(this.pos)){
        if($(".scrollKeep").length==0){
          $(this.$el).scrollTop(this.pos.x);
        }else{
          $(".scrollKeep").scrollTop(this.pos.x);
        }
      }
    })()
  },
  methods:{
    _bindScroll(){
      var self=this;
      var $self=$(this.$el);
      this.$route.meta&&this.$route.meta.keepAlive&&(function(){
        if($self.find(".scrollKeep").length==0){
          $self.unbind();
          $self.on("scroll",function(){
            var x = $(this).scrollTop();
            self.pos.x=x;
          })
        }else{
          $self.find(".scrollKeep").unbind();
          $self.find(".scrollKeep").on("scroll",function(){
            var x = $(this).scrollTop();
            self.pos.x=x;
          });
        }

      })()
    },
  }
}



