import { wxCreateTokenModel } from '@/model'
export default{
  WXshare({title='',desc='',link='',imgUrl=''}){
    var ua = navigator.userAgent.toLowerCase();
    if (ua.match(/MicroMessenger/i) != 'micromessenger')return;
    wxCreateTokenModel.execute({ bizData:{appId:"wx90dee60149e63525",url:location.href}}).then((data)=>{
      let {resultDesc ,resultData} = data.data;
      if(resultDesc=='success'){
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: resultData.appId, // 必填，公众号的唯一标识
          timestamp: resultData.timestamp, // 必填，生成签名的时间戳
          nonceStr: resultData.noncestr, // 必填，生成签名的随机串
          signature: resultData.signature,// 必填，签名
          jsApiList: [
            'updateAppMessageShareData',
            'updateTimelineShareData'
          ]
        });
        wx.ready(function () {
          // 自定义“分享给朋友”
          wx.updateAppMessageShareData({ 
            title: title, // 分享标题
            desc: desc, // 分享描述
            link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
            imgUrl: imgUrl, // 分享图标
            success: function () {
              // 设置成功
            }
          });
    
          wx.updateTimelineShareData({ 
            title: title, // 分享标题
            link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
            imgUrl: imgUrl, // 分享图标
            success: function () {
              // 设置成功
            }
          });
        })
        wx.error(function(res){
          console.log(`wx-error:${res}`)
        })
      }
    })
  }
}


