<template>
  <!-- 系列商品组件 -->
  <div class="series-goods" :style="{width:double?'48%':'100%'}" @click="gotoDetail(goodsData.spu_code)">
    <div class="single-img"><img v-lazy="goodsData.img" alt=""></div>
    <p class="title">{{goodsData.title}}</p>
    <p class="describe">{{goodsData.subTitle}}</p>
    <div class="price">
      <span class="integral">{{goodsData.price.feePricePoint}}</span>
      <span v-show="priceTyepTag(goodsData.price.type)" class="tag">{{goodsData.price.label}}</span>
    </div>
    <div v-show="priceTyepTwo(goodsData.price.type)" class="price">
      <span class="integral" :class="{line:priceTyepLine(goodsData.price.type)}">{{goodsData.price.originPricePoint}}</span>
    </div>
  </div>
</template>
<script>
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import { mapState, mapMutations } from 'vuex'
  import {supreme,getCookie} from '@/supreme-core'
  export default {
    name: "SeriesGoods",
    props:{
      // 是否是双列
      double:{
        type:Boolean,
        default:false
      },
      goodsData:{
        type:Object,
        default:{
          img:'https://cdn-app.nio.com/user/2018/6/16/c7c25051-d1e7-489e-b9a8-81bd06120204.jpg',
          spu_code:'WK001',
          title:'商品主标题',
          subTitle:'商品副标题',
          price:{
            currentUserPricePoint:"30",
            feePricePoint:"20",
            originPricePoint:"30",
            label:'车主价',
            type:1
          }
        }
      }
    },
    methods:{
      ...mapMutations({
        browserOpenNIO:"detailState/browserOpenNIO",
      }),
      // 价格标签
      priceTyepTag(type){
        if(type==1|| type==2){
          return false;
        }else{
          return true;
        }
      },
      // 价格删除线
      priceTyepLine(type){
        if(type==2||type == 6){
          return true;
        }else{
          return false;
        }
      },
      // 第二个价格是否显示
      priceTyepTwo(type){
        if(type==2||type == 5||type == 6){
          return true;
        }else{
          return false;
        }
      },
      gotoDetail(code){
        if(util.isInWebview()){
           // 跳转商品详情
          hybrid.openPage({type:1,shema:`nio://mall/item?version=2&id=${spucode}`});
        }else{
          this.browserOpenNIO(`nio://mall/item?version=2&id=${code}`);
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
.series-goods{
  margin-top: 30px;
  width: 100%;
  .single-img{
    display:flex;
    align-items:center; 
    justify-content:center;
    width: 100%;
    height: 1.58rem;
    // background-color: #f40;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    img[lazy=loading]{
      width: 40px;
      height: 40px;
    }
  }
  .title{
    font-size: 14px;
    color: #363C54;
    margin:6px 0;
  }
  .describe{
    font-size: 12px;
    color: #9B9DA9;
    line-height: 14px;
    margin-bottom: 5px;
  }
  .price{
    display: flex;
    align-items: center;
    color: #9b9da9;
    .integral{
      font-size: 14px;
      height: 14px;
      line-height: 14px;
      padding-left: 16px;
      background: url('../../assets/svg/icon_integral2.svg') left center no-repeat;
      background-size: 12px;
      font-weight: 300;
      &.line{
        text-decoration:line-through;
      }
    }
    .tag{
      margin-left: 10px;
      font-size: 12px;
      padding: 2px 10px;
      background-color: #F7F7F8;
      border-radius:10px;
      transform: scale(0.9);
    }
  }
}
</style>



