/**
 * @description 手势处理提供一个简易的处理接口,方便用户判断手势 上下左右 和长按压，并提供方法回调
 * 如果这些不能满足请直接在页面中使用this.hammer 处理
 * @author lei.you
 * @date 2018/3/8
 * */
require("../../../util/hammer")
import gesture from '../../../util/gesture'
import {type} from '../../../util/util'
export default {
  //????关于重复绑定的处理？？？
  mounted(){
    /**
     * swipe: 滑动; 和 pan 类似,但滑动更快速,无粘滞.
     swipeleft,swiperight,swipeup,swipedown: 左滑,右滑,上滑,下滑

     * rotate 实现旋转效果，如旋转图片
     pinch/zoom 放大、缩小
     press  长时间按压触发效果
     pan 左右滑动
     tap 点击
     swipe 滑动
     * */
    var hammer = new Hammer(this.$el);
    this.hammer=hammer;
    this.initPanStartEnd();
    this.isTouchDirection();
    this.isPress();

    this.tempDirectionMethod=null;
    this.startPoint=null;
  },
  methods:{
    initPanStartEnd(){
      this.hammer.on("panstart panend ", ev=>{
        //this[gesture.TOUCH_LEFG_METHOD]();
        if(ev.type=="panend"){

          if(this.isBackGesture()){
            this.tempDirectionMethod=gesture.TOUCH_BACK_METHOD;
            if(type.isFunction(this[this.tempDirectionMethod])){
              this[this.tempDirectionMethod]()
            }
          }else{
            if(type.isFunction(this[this.tempDirectionMethod])){
              this[this.tempDirectionMethod]()
            }
          }
          this.tempDirectionMethod=null;
          this.startPoint=null;
        }

        if(ev.type=="panstart"){
          this.startPoint=ev.center;
          console.log(this.startPoint);
        }
      });
    },
    // touch 的方向
    isTouchDirection(){
      this.hammer.on("panleft panright panup pandown", ev=>{
        if(ev.type=="panleft"){
          this.tempDirectionMethod=gesture.TOUCH_LEFG_METHOD;
        }else if(ev.type=="panright"){
          this.tempDirectionMethod=gesture.TOUCH_RIGHT_METHOD;
        }else if(ev.type=="panup"){
          this.tempDirectionMethod=gesture.TOUCH_UP_METHOD;
        }else if(ev.type=="pandown"){
          this.tempDirectionMethod=gesture.TOUCH_DOWN_METHOD;
        }

      });
    },
    //判断用户是不是在做回退手势
    isBackGesture(){
      return this.startPoint.x<30;
    },
    // 是否长按
    isPress(){
      this.hammer.on("press", ev=>{
        if(type.isFunction(this[gesture.PRESS_METHOD])){
          this[gesture.PRESS_METHOD]()
        }
      });
    }
  }

}



