<template>
  <div class="banner_box">
    <swiper :options="swiperOption()" class="banner" ref="swiperBanner">
      <swiper-slide class="banner_img" v-for="(item, index) in pictureList" :key="index">
        <img :src="item.enlargedURL" alt>
      </swiper-slide>
      <div v-show="pictureList&&pictureList.length>1" class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <p v-show="!selectedSku.available" class="sold_out">已下架</p>
    <p v-show="selectedSku.isSoldOut&&selectedSku.available" class="sold_out">已售罄</p>
  </div>
</template>

<script>
import { mapState } from "vuex";
import hybrid from "@/util/reactNativeHybrid";
import util from "@/util/util";
import { supreme } from "@/supreme-core";
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name: "swipeBanner",
  computed: {
    ...mapState({
      pageStatus: state => state.detailState.pageStatus,
      detail: state => state.detailState.data
    }),
    selectedSku() {
      return this.detail && this.detail.product.selectedSku;
    },
    pictureList() {
      return this.detail && this.detail.product.selectedSku.pictureList;
    },
    imagesArray() {
      var array = [];
      this.pictureList &&
        this.pictureList.forEach((pic, ind) => {
          array.push(pic.enlargedURL);
        });
      return array;
    },
    swiperBanner() {
      return this.$refs.swiperBanner.swiper;
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  methods: {
    swiperOption() {
      let slef = this;
      return {
        pagination: {
          el: ".swiper-pagination"
        },
        autoplay: {
          delay: 5000, //5秒切换一次
          disableOnInteraction: false //用户操作后不停止自动切换
        },
        on: {
          click: function() {
            let realIndex = this.realIndex;
            slef.preview(realIndex);
          }
        },
        loop: slef.pictureList && slef.pictureList.length > 1
      };
    },
    preview(index) {
      if (util.isInWebview()) {
        // banner埋点
        hybrid.buriedPointClick({
          eventKey: "MallItemPage_Banner_Click",
          currentPage: "MallItemPage_",
          NextPage: "",
          params: {
            spu_code: supreme.getQueryString("spuCode")
          }
        });
        hybrid.showImagesPreview({
          images: this.imagesArray,
          index: index
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.banner_box {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 3.75rem;
}
.banner {
  width: 100%;
  height: 3.75rem;
}
.banner_img {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 100%;
    height: 100%;
    background: url("../../assets/svg/img_loading.svg") center no-repeat;
    background-size: 80px;
  }
}
.sold_out {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #ffffff;
  font-size: 18px;
  background: rgba(4, 11, 41, 0.4);
  z-index: 2;
}
</style>



