<template>
  <!-- 文描组件 -->
  <div class="introduce_box">
    <div ref="introduce" class="introduce" v-html="content"></div>
  </div>
</template>
<script>
  import util from '@/util/util'
  import hybrid from '@/util/reactNativeHybrid'
  import {supreme} from '@/supreme-core'
  require("../util/hammer.js")
  export default {
    name: "describe",
    props: {
      content: {
        type: String,
        default: true
      }
    },
    mounted(){
      setTimeout(e=>{
        this.removeEventImages();
        this.addEventImages();
      })
    },
    beforeDestroy(){
      this.removeEventImages();
    },
    methods:{
      removeEventImages:function(){
        // let doms = this.$refs.introduce;
        // if(doms){
        //   doms.removeEventListener('click', this.showImages, false);
        // }
        if(this.imageTap){
          this.imageTap.off("tap");
        }
      },
      addEventImages:function(){
        let doms = this.$refs.introduce;
        let imgs = doms.getElementsByTagName("img");
        this.decImagesArr = [];
        for(let j=0;j<imgs.length;j++){
          imgs[j].dataset.imgIndex=j;
          this.decImagesArr.push(imgs[j].src)
        }
        var imageTap = new Hammer(doms);
        this.imageTap = imageTap;
        imageTap.on('tap', this.showImages,false);
        //doms.addEventListener( 'click', this.showImages, false);
      },
      showImages(dom){
        if(dom.target.nodeName=='IMG'&&dom.target.dataset.imgIndex!=null){
          hybrid.showImagesPreview({
            "images":this.decImagesArr,
            "index": parseInt(dom.target.dataset.imgIndex)
          })
        }else{
          console.log('点击的不是图片')
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  .introduce_box{
    position: relative;
  }
  .introduce{
    overflow: hidden;
    margin: 22px 25px 0;
    font-size: 14px;
    // .video_box{
    //   position: relative;
    //   left:0;
    //   top:0;
    //   width: 100%;
    //   margin-bottom: .4rem;
    //   .video_play_but{
    //     position: absolute;
    //     left: 50%;
    //     top: 50%;
    //     margin-left: -.3rem;
    //     margin-top: -.3rem;
    //     width: .6rem;
    //     height: .6rem;
    //     border-radius: .3rem;
    //     background: url('../assets/images/icon_video_play.png') rgba(0,0,0,0.4) center no-repeat;
    //   }
    // }
    // .text{
    //   line-height: .3rem;
    //   font-size: .16rem;
    //   color: #363C54;
    //   margin-bottom: .2rem;
    // }
    // .images{
    //   width: 100%;
    // }
    // video{
    //   width: 100%;
    // }
  }
  .introduce_mask{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    // background-color: #00BCBC;
  }
</style>



