import Vue from 'vue'
import Router from 'vue-router'
import baseConfig from '@/config/baseConfig'
import util from '@/util/util'

import detail from '@/views/detail/Index'
import article from '@/views/article/Index'
import seriesPage from '@/views/seriesPage/Index'
import productParameters from '@/views/productParameters/Index'
import errorPage from '@/views/errorPage/Index'
// const detail = () => import('@/views/detail/Index');
// const article = () => import('@/views/article/Index');
// const seriesPage = () => import('@/views/seriesPage/Index');
// const productParameters = () => import('@/views/productParameters/Index');
// const errorPage = () => import('@/views/errorPage/Index');

Vue.use(Router);

export default new Router({
  mode: baseConfig.routerModel,
  routes: [
    {
      path: '/',
      redirect:{path: util.basePath()+'/detail'}
      // component: detail
    },
    {
      path: util.basePath()+'/detail',
      name:'detail',
      component:detail,
      meta:{keepAlive: true}
    },
    {
      path: util.basePath()+'/article',
      name:'article',
      component:article,
      meta:{keepAlive: true}
    },
    {
      path: util.basePath()+'/series',
      name:'series',
      component:seriesPage,
      meta:{keepAlive: true}
    },
    {
      path: util.basePath()+'/product_parameters',
      name:'productParameters',
      component:productParameters
    },
    {
      path:'*',
      name:'error',
      component:errorPage
    }
  ]
})




