<template>
  <div class="series-describe" :style="{paddingBottom:!isShowArrow?'10px':0}">
    <p ref="textDome" :class="{truncation:!unfold}">{{getDescribe.text}}</p>
    <span v-show="isShowArrow" @click="unfold = !unfold" class="arrow" :class="{up:unfold}"></span>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  export default {
    name: "SeriesDescribe",
    data(){
      return{
        unfold:true,
        isShowArrow:true
      }
    },
    props:['componentKey'], 
    computed:{
      ...mapState({
        'seriesState': state => state.seriesState.data,
      }),
      getDescribe(){
        let describe = '';
        this.seriesState.seriesList.forEach(item => {
          if(item.type == 'richText'&&item.sortKey==this.componentKey){
            describe = item.data;
          }
        });
        return describe;
      }
    },
    mounted(){
      if(this.$refs.textDome&&this.$refs.textDome.clientHeight > 60){
        this.unfold = false;
        this.isShowArrow = true;
      }else{
        this.isShowArrow = false;
      }   
    },
    methods:{
    }
  }
</script>

<style lang="scss" scoped>
.series-describe{
  margin: 25px 25px 0;
  font-size: 14px;
  line-height: 20px;
  color: #363C54;
  display: flex;
  flex-direction: column;
  word-break:break-all; 
  border-bottom: 1px solid #F0F1F2;
  .truncation{
    overflow: hidden;
    text-overflow: ellipsis;
    display:-webkit-box;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    -webkit-line-clamp:3; //显示的
  }
  .arrow{
    height: 32px;
    background: url('../../assets/svg/icon_down_arrow.svg') center no-repeat;
    &.up{
      background: url('../../assets/svg/icon_up_arrow.svg') center no-repeat;
    }
  }
}
</style>



