<template>
  <div class="goods-double-row">
    <SeriesGoods :double="true" v-for="(dataItem,index) in getGoodsList" :key="index" :goods-data="dataItem" />
    <!-- <SeriesGoods /> -->
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  import SeriesGoods from './SeriesGoods.vue'
  export default {
    name: "GoodsDouble",
    components:{ SeriesGoods },
    props:['componentKey'],
    computed:{
      ...mapState({
        'seriesState': state => state.seriesState.data,
      }),
      getGoodsList(){
        let goodsList = [];
        this.seriesState.seriesList.forEach(item => {
          if(item.type == 'gridGoodsItem'&&item.sortKey==this.componentKey){
            goodsList = item.data;
          }
        });
        return goodsList;
      }
    },
    created(){
      // console.log(this.getGoodsList)
    },
    methods:{
    }
  }
</script>

<style lang="scss" scoped>
.goods-double-row{
  display: flex;
  margin: 0 25px;
  flex-wrap: wrap;
  justify-content: space-between;
}
</style>



