<template>
  <div id="app">
    <router-view  class="view toast"  v-if="!$route.meta.keepAlive"></router-view>
    <keep-alive ref="keepAlive">
      <router-view  class="view"  v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <NotWebView :page-type="$route.path" />
  </div>
</template>

<script>
  import NotWebView from '@/components/NotWebView.vue'

  export default {
  name: 'app',
  components:{
    NotWebView
  },
  created(){
  },
}
</script>

