  @import "./assets/scss/base";
  #app {
    height: 100%;
    font-size: 12px;
  }
  .mint-indicator{
    z-index: 1000;
  }
  // 覆盖 mint 原生的loading效果
  .mint-indicator-wrapper {
    z-index: 1002;
    background-color: transparent !important;
    background-image: url(assets/images/Spin.gif) !important;
    background-size: cover !important;
    width: 65px !important;
    height: 65px !important;
    .mint-indicator-spin {
      display: none !important;
    }
    .mint-indicator-text{
      display: none !important;
    }
  }
  .mint-indicator-mask{
    z-index: 1001;
  }

