function apiurl(url){
  return 'api/v1/otd/mer/service-aggregation/'+url;
}

export default {
  login: 'doLogin',
  ssoLogin: 'cl/app/sso/login',
  wx_create_token:"api/v1/platform/svcaggr/aggr-middle/noauth/exterior/wechat/getSignature",
  creditShare: apiurl("credit/share"),
  detailbysa:apiurl("product/noauth/detailv2"),
  addCartBySa:apiurl("cart/addbysa"),
  detailCommentLiked:apiurl("comments/commentliked"),
  cartGetcartCount:apiurl('cart/getcartcount'),
  userCredits:apiurl("userinfo/credits"),
  detailCommentList:apiurl("product/noauth/comment"),
  uploadReadingReport:apiurl("article/noauth/readreport"),
  follow:apiurl("userinfo/setuserrelation"),
  /* 文章页 */
  article:apiurl("article/noauth/detail"),
  getcomments:apiurl("article/noauth/comments"),
  addcomment:apiurl('article/addcomment'),
  addreplyto:apiurl('article/addreplyto'),
  articleliked:apiurl('article/articleliked'),
  commentliked:apiurl('article/commentliked'),
  deletecomment:apiurl('article/deletecommentv2'),
  reportcomment:apiurl('article/reportcomment'),
  /* 合辑页 */
  series:apiurl('series/noauth/index')
}



