import env from '@/config/envConfig.json'
import {supreme, getCookie} from '@/supreme-core'
import baseConfig from '@/config/baseConfig'

export default {
  getEnv(){
    // 0 qa 1 uat 2 stg 3 prod
    var url= location.href;
    var code;
    if(location.protocol=="file:"){
      var osInfo;
      try{
        osInfo=dsBridge.call("common.getAppInfo","")
      }catch (e){
        code=3;
      }
      if(osInfo&&osInfo.env){
        if(osInfo.env=='qa'){
          code=0;
        }else if(osInfo.env=='sit'||osInfo.env=='uat'){
          code=1;
        }else if(osInfo.env=='stg'){
          code=2;
        }else{
          code=3;
        }
      }
      return code;
    }
    // return 1;
    if(url.indexOf('-qa.')!=-1||url.indexOf('localhost')!=-1||url.indexOf('127.0.0.1')!=-1 ){
      code=0;
    }else if(url.indexOf('-uat.')!=-1||url.indexOf("-sit.")!=-1){
      return code=1;
    }else if(url.indexOf('-stg.')!=-1){
      return code=2;
    }else{
      return code=3;
    }
    return code;
  },
  basePath(){
    let pathName=baseConfig.compatible.isSupportHybrid? baseConfig.compatible.prefix+ baseConfig.compatible.module:"";
    if(this.newVersion()){
      return pathName;
    }else{
      return '';
    }
  },
  formatDate (str) {//格式化时间
    let paramsDate = new Date(str*1000);
    let currentDate = new Date();
    let time = currentDate.getTime() - paramsDate.getTime(); //现在的时间-传入的时间 = 相差的时间（单位 = 毫秒）
    let paramsMonth = paramsDate.getMonth()+1<10?`0${paramsDate.getMonth()+1}`:paramsDate.getMonth()+1;
    let paramsDay = paramsDate.getDate()+1<10?`0${paramsDate.getDate()}`:paramsDate.getDate();
    let defaultData =  `${paramsDate.getFullYear()}-${paramsMonth}-${paramsDay}`;
    if (time < 0) {
        return '';
    } else if (time / 1000 < 60) {
        return '刚刚';
    } else if ((time / 60000) < 60) {
        return parseInt((time / 60000)) + ' 分钟前';
    } else if ((time / 3600000) < 24) {
        return parseInt(time / 3600000) + ' 小时前';
    // } else if ((time / 86400000) < 31) {
    //     return parseInt(time / 86400000) + '天前';
    // } else if ((time / 2592000000) < 1) {
    //     return parseInt(time / 2592000000) + '月前';
    }else if((time / 2592000000) < 12 && paramsDate.getFullYear()==currentDate.getFullYear()){
        // 大于24小时 今年内
        return `${paramsMonth}-${paramsDay}`;
    } else {
        return defaultData;
        // return parseInt(time / 31536000000) + '年前';
    }
    return defaultData;
  },
  getEnvInfo(){
    switch (this.getEnv()){
      case 0:
        return env.qa;
      case 1:
        return env.uat;
      case 2:
        return env.stg;
      case 3:
        return env.prod;
      default:
        return env.qa;
    }
  },
  // 是否在app 的webview 中，浏览器，微信，微博等载体不算
  isInWebview () {
    if(this.newVersion()){
      return  dsBridge&&dsBridge.hasNativeMethod("common.showToast");
    }
    var ua = navigator.userAgent.toLowerCase()
    if (ua.match(/MicroMessenger/i) == 'micromessenger') { // 微信浏览器判断
      return false
    } else if (ua.match(/QQ/i) == 'qq') { // QQ浏览器判断
      return false
    } else if (ua.match(/WeiBo/i) == "weibo") {
      return false
    } else {
      if (ua.match(/Android/i) != null) {
        return ua.match(/browser/i) == null&&!supreme.getQueryString("share")
      } else if (ua.match(/iPhone/i) != null) {
        return ua.match(/safari/i) == null&&!supreme.getQueryString("share")
      } else {
        return (ua.match(/macintosh/i) == null && ua.match(/windows/i) == null)&&!supreme.getQueryString("share")
      }
    }
  },
  // 兼容刘海屏手机
  isIphoneX(){
    if(this.newVersion()){
      let osinfo = supreme.localCache.get("getOSInfo");
      return osinfo&&osinfo['device_type']=='ios'&&osinfo['is_iphonex_type']?true:false;
    }
    let appInfo = JSON.parse(getCookie('appInfo')),
        deviceIdArr = ['iPhone10,3','iPhone10,6','iPhone11,2','iPhone11,4','iPhone11,6','iPhone11,8'],
        state = false;
    if(appInfo==null){
      return false;
    }
    for(let value of deviceIdArr){
      if(value==appInfo['mobileinfo']['deviceId']){
        state = true;
      }
    }
    if(state){
      return true;
    }else{
      return false;
    }
  },
  isToken(){
    if(this.newVersion()){
      let userToken = dsBridge.call("common.getUserInfo", "");
      // alert(JSON.stringify(userToken['access_token:']))
      return userToken&&userToken['access_token']?true:false;
    }
    if(getCookie('userToken') && getCookie('userToken') != null){
        return true;
    }else{
        return false;
    }
  },
  toNum(a){
    var a=a.toString();
    //也可以这样写 var c=a.split(/\./);
    var c=a.split('.');
    var num_place=["","0","00","000","0000"],r=num_place.reverse();
    for (var i=0;i<c.length;i++){ 
        var len=c[i].length;       
        c[i]=r[len]+c[i];  
    } 
    var res= c.join(''); 
    return res; 
  },
  newVersion(version = '3.3.5'){ 
    if(dsBridge.hasNativeMethod("common.getAppInfo")){
      let appInfo = dsBridge.call("common.getAppInfo","")
      if(appInfo){
        let app_version = appInfo.version;
        let _a=this.toNum(version),
            _b=this.toNum(app_version); 
        if(_a<=_b){
          return true;
        }else{
          return false;
        }    
      }
    }
  }
}



