<template>
 <!-- 长按选项 -->
  <div class="options-box" 
    @touchstart.stop="touchstartFun" 
    @touchmove.stop="touchmoveFun" 
    @touchend.stop="touchendFun" 
    :style="{backgroundColor:backgroundHint?'#CDCED4':'transparent'}">
    <div v-show="visible" class="options-select">
      <span class="arrow"></span>
      <ul>
        <li v-for="(item,index) in optionsText" 
          @click.stop="clickTabEmit(item)"
          :style="{
            borderLeft:optionsText.length>=3&index==2?'1px solid #9B9DA9':'none',
            borderRight:optionsText.length>=2&&index==0?'1px solid #9B9DA9':'none'}" 
          :key="index">{{item.text}}</li>
        <!-- <li>复制</li>
        <li>删除</li> -->
      </ul>
    </div>
    <slot></slot>
  </div>
</template>
<script>
  export default {
    name: "options-tab",
    data(){
      return{
        visible:false,
        backgroundHint:false
      }
    },
    props: {
      optionsText: {
          type: Array,
          //text必须
          default: [
            {
              type:1,
              text:'回复'
            },
            {
              type:2,
              text:'复制'
            },
            {
              type:3,
              text:'删除'
            }
          ]
      }
    },
    computed:{},
    mounted(){
      document.addEventListener('touchstart',this.isHideTab,false)
    },
    beforeDestroy(){
      document.removeEventListener('touchstart',this.isHideTab,false);
    },
    methods:{
      clickTabEmit(item){
        this.$emit('clickTab', item);
        this.visible = false;
        this.backgroundHint = false;
      },
      touchstartFun(e){
        let self = this;
        this.backgroundHint = true;
        this.timer = setTimeout(function () {
          self.visible = !self.visible;
        },500)
      },
      touchmoveFun(e){
        clearTimeout(this.timer);
      },
      touchendFun(e){
        if(!this.visible){
          this.backgroundHint = false;
        }
        clearTimeout(this.timer);
      },
      isHideTab(){
        if( this.visible ){
          this.visible= false;
          this.backgroundHint = false;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .options-box{
    position: relative;
    background-color: #CDCED4;
    .options-select{
      position: absolute;
      top: -43px;
      left: 50%;
      margin-left: -25%;
      .arrow{
        position: absolute;
        left: 50%;
        top: 36px;
        margin-left: -6px;
        width: 0;
        height: 0;
        border-width: 6px;
        border-style: solid;
        border-color: rgba($color: #363C54, $alpha: 0.8) transparent transparent transparent;
      }
      ul{
        display: flex;
        flex-direction: row;
        border-radius: 10px;
        background-color:rgba($color: #363C54, $alpha: 0.8);
        li{
          margin: 0;
          width: 54px;
          height: 36px;
          line-height: 36px;
          color: #fff;
          text-align: center;
          // &:first-child{
          //   border-right: 1px solid #9B9DA9;
          // }
          // &:last-child{
          //   border-left: 1px solid #9B9DA9;
          // }
        }
      }
    }
  }
</style>



