<template>
  <div class="series-banner">
    <img v-lazy="getBanner.img" alt="">
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  export default {
    name: "SeriesBanner",
    props:['componentKey'],
    computed:{
      ...mapState({
        'seriesState': state => state.seriesState.data,
      }),
      getBanner(){
        let banner = '';
        this.seriesState.seriesList.forEach(item => {
          if(item.type=='banner'&&item.sortKey==this.componentKey){
            banner = item.data[0];
          }
        });
        return banner;
      }
    },
    methods:{
    }
  }
</script>

<style lang="scss" scoped>
  .series-banner{
    display:flex;
    align-items:center; 
    justify-content:center;
    width: 100%;
    height: 3.75rem;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    img[lazy=loading]{
      width: 40px;
      height: 40px;
    }
  }
</style>



