<template>
  <!-- 文章商品 -->
  <div class="article-goods" :style="{borderBottom:haveLine&&'1px solid #F0F1F2'}" @click="gotoGoodsDetail(getGoodsData.spu_code)">
    <div class="banner">
      <img  v-lazy="getGoodsData.img" alt="">
    </div>
    <div class="buy-title">
      <p>{{getGoodsData.title}}</p>
      <span class="buy_but" @click.stop="goodsBuyBut(getGoodsData.spu_code)">购买</span>
    </div>
    <div v-show="getGoodsData.price.type!=5" class="price">
      <span class="integral">{{getGoodsData.price.feePricePoint}}</span>
      <span v-show="priceTyepTag(getGoodsData.price.type)" class="tag">{{getGoodsData.price.label}}</span>
    </div>
    <div class="price">
      <span v-show="priceTyepTwo(getGoodsData.price.type)" class="integral" :class="{line:priceTyepLine(getGoodsData.price.type)}">
        {{getGoodsData.price.originPricePoint}}
      </span>
    </div>
    <!-- type == 5 车主价在下面显示 -->
    <div v-show="getGoodsData.price.type==5" class="price">
      <span class="integral">{{getGoodsData.price.feePricePoint}}</span>
      <span v-show="priceTyepTag(getGoodsData.price.type)" class="tag">{{getGoodsData.price.label}}</span>
    </div>
  </div>
</template>
<script>
  import { mapState, mapMutations } from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import {supreme,getCookie} from '@/supreme-core'
  export default {
    name: "ArticleGoods",
    data(){
      return{

      }
    },
    props:{
      componentKey:{
        type:Number,
        default:0
      }
    },
    computed:{
      ...mapState({
        'articleDetail': state => state.articleState.data,
      }),
      getGoodsData(){
        let goodsData = {};
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='single_goods' && item.sortKey==this.componentKey){
            goodsData = item.data;
          }
        })
        return goodsData;
      },
      haveLine(){
        let data = true;
        this.articleDetail.articleComponent.forEach(item=>{
          if(item.type=='single_goods'&& item.sortKey==this.componentKey){
            data = item.haveLine;
          }
        })
        return data;
      },
    },
    methods:{
      ...mapMutations({
        browserOpenNIO:"detailState/browserOpenNIO",
      }),
      // 价格标签
      priceTyepTag(type){
        if(type==1|| type==2){
          return false;
        }else{
          return true;
        }
      },
      // 价格删除线
      priceTyepLine(type){
        if(type==2||type == 6){
          return true;
        }else{
          return false;
        }
      },
      // 第二个价格是否显示
      priceTyepTwo(type){
        if(type==2||type == 5||type == 6){
          return true;
        }else{
          return false;
        }
      },
      gotoGoodsDetail(spucode){
        if(util.isInWebview()){
          // 跳转商品详情
          hybrid.openPage({type:1,shema:`nio://mall/item?version=2&id=${spucode}`});
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      },
      goodsBuyBut(spucode){
        if(util.isInWebview()){
          // 商品购买按钮 点击 埋点
          hybrid.buriedPointClick({
            eventKey:'MallContentPage_Buy_Click',
            currentPage:'MallContentPage',
            NextPage:'',
            params:{
              content_id:this.getQueryString('pageCode'),
            }
          })
          // 跳转商品详情
          hybrid.openPage({type:1,shema:`nio://mall/item?version=2&id=${spucode}`});
        }else{
          this.browserOpenNIO(`nio://mall/article?pageCode=${supreme.getQueryString("pageCode")}`);
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .article-goods{
    margin:0 25px;
    padding-bottom: 20px;
    // border-bottom: 1px solid #F0F1F2;
    .banner{
      display:flex;
      align-items:center; 
      justify-content:center;
      width: 3.25rem;
      height: 3.25rem;
      overflow: hidden;
      text-align: center;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      img[lazy=loading] {
        width: 40px;
        height: 40px;
      }   
    }
    .buy-title{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 10px;
      p{
        flex: .98;
        overflow: hidden;
        text-overflow: ellipsis;
        display:-webkit-box;
        /*! autoprefixer: off */
        -webkit-box-orient: vertical;
        /* autoprefixer: on */
        -webkit-line-clamp:1;
        font-size: 18px;
        color: #363C54;
      }
      .buy_but{
        width: 65px;
        height: 30px;
        line-height: 30px;
        border-radius: 15px;
        background-color: #E6FAFA;
        color: #00BCBC;
        font-size: 14px;
        text-align: center;
      }
    }
    .price{
      display: flex;
      align-items: center;
      color: #9b9da9;
      .integral{
        font-size: 14px;
        height: 14px;
        line-height: 14px;
        padding-left: 16px;
        background: url('../../assets/svg/icon_integral2.svg') left center no-repeat;
        background-size: 12px;
        font-weight: 300;
        &.line{
          text-decoration:line-through;
        }
      }
      .tag{
        margin-left: 10px;
        font-size: 12px;
        padding: 2px 10px;
        background-color: #F7F7F8;
        border-radius:10px;
        transform: scale(0.9);
      }
    }
  }
</style>



