<template>
    <ul class="comments" v-if="comments.list.length>0">
        <li v-for="(item,index) in comments.list" :key="index" @click="gotoUserCommentsDetails(item.id)">
            <div class="userinfo">
                <div class="haeder" @click.stop="gotoUserDetails(item.userId, item.id)">
                    <img :src="item.userAvator" alt="">
                    <span class="icon" 
                        :class="[
                            item.userIdentity=='intentional_owner'||item.userIdentity=='pre_owner'?'is_deposit':'',
                            item.userIdentity=='owner'||item.userIdentity=='coowner'?'is_owner':'']"></span>
                </div>
                <span class="name">{{item.userName}}</span>
                <span v-show="item.userIsNioAuthorized" class="icon_employees"></span>
                <span @click.stop="FollowBut(item)" v-show="item.userRelation > -1" class="follow" :class="{but:item.userRelation==0||item.userRelation==3}">
                    {{item.userRelation==0&&'关注'||item.userRelation>3 &&'关注'|| item.userRelation==1&&'已关注' || item.userRelation==3&&'聊天'}}
                </span>
            </div>
            <p class="comments_text">{{item.content}}</p>
            <div class="comments_photo" v-if="item.pic.length>0">
                <div v-show="picIndex<3" v-for="(picItem, picIndex) in item.pic" 
                    :key="picIndex" class="photo_li" @click.stop="preview(index, picIndex, item.id)" :style="{marginRight: picIndex<2&&'10px'}">
                    <img v-lazy="picItem.thumbnailURL" alt="">
                </div>
                <span class="photo_num" v-show="item.pic.length>3">{{item.pic.length}}</span>
            </div>
            <div class="comments_information">
                <span class="time">{{item.time | formatDate}}</span>
                <div class="comments_information_right">
                    <span @click="commentsBtn(item.id)" class="review">{{item.commentCount}}</span>
                    <span @click.stop="likeBtn(item.id,item.hasLike)" class="praise" :class="{select:item.hasLike}">{{item.likeCount}}</span>
                </div>
            </div>
        </li>
    </ul>
</template>

<script>
  import {mapState,mapMutations} from 'vuex'
  import hybrid from '@/util/reactNativeHybrid'
  import util from '@/util/util'
  import {supreme,getCookie,type} from '@/supreme-core'
  import { FollowModel , detailCommentLikedModel} from '@/model'

  export default {
    name: "userComments",
    computed:{
      ...mapState({
        'comments': state => state.detailState.comments
      })
    },
    filters: {
        formatDate:util.formatDate
    },
    methods:{
        ...mapMutations({
            browserOpenNIO:"detailState/browserOpenNIO"
        }),
        gotoUserCommentsDetails(id){
            if(util.isInWebview()){
                // 单条晒图点击埋点
                hybrid.buriedPointClick({
                    eventKey:'MallItemPage_PostItem_Click',
                    currentPage:'MallItemPage_',
                    NextPage:'',
                    params:{
                        spu_code:supreme.getQueryString("spuCode"),
                        post_id:id
                    }
                })
                // 页面离开埋点
                hybrid.trackEndPage();
                // 跳转评论详情
                hybrid.openPage({type:1,shema:`nio://ugc/detail?type=comment&id=${id}&scroll=0`})
            }else{
                this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
            }
        },
        gotoUserDetails(userId, id){            
            if(util.isInWebview()){
                 // 判断Token 是否登录
                if(!util.isToken()){
                    hybrid.openLoginPage();
                    return;
                }
                // 单条晒图点击埋点
                hybrid.buriedPointClick({
                    eventKey:'MallItemPage_PostItem_User_Click',
                    currentPage:'MallItemPage_',
                    NextPage:'',
                    params:{
                        spu_code:supreme.getQueryString("spuCode"),
                        post_id:id,
                        targetaccountid:userId
                    }
                })
                // 页面离开埋点
                hybrid.trackEndPage();
                // 跳转
                hybrid.openPage({type:1,shema:`nio://friend/info?userID=${userId}`})
            }else{
                this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
            }
        },
        // 预览评论图片
        preview(liIndex,index,id){
            if(util.isInWebview()){
                // 单条晒图点击埋点
                hybrid.buriedPointClick({
                    eventKey:'MallItemPage_PostItem_Image_Click',
                    currentPage:'MallItemPage_',
                    NextPage:'',
                    params:{
                        spu_code:supreme.getQueryString("spuCode"),
                        post_id:id
                    }
                })
                // 展示图片
                var array=[];
                this.comments.list[liIndex].pic&&this.comments.list[liIndex].pic.forEach((item,ind)=>{
                    array.push(item.enlargedURL);
                });
                //  console.log(array)
                hybrid.showImagesPreview({
                    "images":array,
                    "index":index
                })
            }else{
                this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
            }
       },
       commentsBtn(id){
           if(util.isInWebview()){
                // 单条晒图点击埋点
                hybrid.buriedPointClick({
                    eventKey:'MallItemPage_PostItem_Comment_Click',
                    currentPage:'MallItemPage_',
                    NextPage:'',
                    params:{
                        spu_code:supreme.getQueryString("spuCode"),
                        post_id:id
                    }
                })
                // 页面离开埋点
                hybrid.trackEndPage();
                console.log('评论')
           }
       },
       likeBtn(id, haslike){
           if(util.isInWebview()){
                // 判断Token 是否登录
                if(!util.isToken()){
                    hybrid.openLoginPage();
                    return;
                }
                // 单条晒图点击埋点
                hybrid.buriedPointClick({
                    eventKey:'MallItemPage_PostItem_Like_Click',
                    currentPage:'MallItemPage_',
                    NextPage:'',
                    params:{
                        spu_code:supreme.getQueryString("spuCode"),
                        post_id:id
                    }
                })
                var self = this;
                // 评论点赞
                detailCommentLikedModel.execute({data:{commentId:id, isLiked:!haslike}}).then((data)=>{
                    let {resultDesc, resultData} = data.data;
                    if(resultDesc=='success'){
                        self.comments.list.find(item=>{
                            if(item.id == id){
                                item.hasLike = !item.hasLike;
                                item.hasLike?item.likeCount++:item.likeCount--;
                            }
                        })
                    }else{
                        hybrid.showToast({content:'操作失败！'});
                    }
                })
           }else{
                this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
           }
        },
        FollowBut(item){
            if(util.isInWebview()){
                // 判断Token 是否登录
                if(!util.isToken()){
                    hybrid.openLoginPage();
                    return;
                }
                switch(item.userRelation){
                    case -1:
                        console.log('不能关注自己');
                        return;
                        break;
                    case 0:
                        // console.log('发起关注请求！')
                        this.followFun(item)
                        break;
                    case 1:
                        console.log('已关注');
                        return;
                        break;
                    case 3:
                        hybrid.openPage({type:1,shema:`nio://conversation?id=${item.imUser.imId}&name=${encodeURI(item.imUser.nick)}&type=private`});                        console.log('互相关注,跳转到聊天');
                        return;
                        break;
                    case 4:
                        console.log('不可取消关注，存在于好友列表');
                        return;
                        break;
                    case 5:
                        console.log('不可关注，存在于最近联系人中');
                        return;
                        break;
                    case 6:
                        console.log('不可关注，群成员中可能出现');
                        return;
                        break;
                }
            }else{
                this.browserOpenNIO(`nio://mall/item?version=2&id=${supreme.getQueryString("spuCode")}`);
            }        
        },
        followFun(userItem){
             // 只有 0 才会发起关注接口
            let params = {
                data:{
                    type:"follow",
                    accountId:userItem.userId
                }
            }
            if(type.isString(params["data"])){
                try{
                    params["data"]=JSON.parse(params["data"]);
                }catch (ex){
                    throw ex;
                    return ;
                }
            }
            FollowModel.execute(params).then(data=>{
                let {resultDesc, resultData} = data.data;
                if(resultDesc == 'success'){
                    this.comments.list.find(item=>{
                        if(item.userId==userItem.userId){
                            item.userRelation = resultData;
                        }
                    })
                }
            }).catch(err=>{
                console.log(err)
            });
        }
    }
  }
</script>

<style lang="scss" scoped>
    .comments{
        padding:0 25px 0;
        li{
            padding: 30px 0;
            border-bottom:1px solid #F0F1F2;
            &:last-child{
                padding-bottom: 20px;
            }
            &:first-child{
                padding-top: 20px;
            }
        }
    }
    .userinfo{
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-bottom: 15px;
        .haeder{
            position: relative;
            left: 0;
            top: 0;
            display: flex;
            width: 40px;
            height: 40px;
            border-radius: 20px;
            margin-right: 10px;
            line-height: 40px;
            text-align: center;
            background-color: #F7F7F8;
            img{
                width: 100%;
                height: 100%;
                border-radius: 20px;
                vertical-align: middle;
            }
            .icon{
                position: absolute;
                right: 0;
                bottom: 0;
                z-index: 11;
                width: 14px;
                height: 14px;
                border-radius: 7px;
            }
            .is_owner{
                background: url('../../assets/svg/icon_nio_vip.svg') center no-repeat;
                background-size: cover;
            }
            .is_deposit{               
                background: url('../../assets/svg/icon_deposit_vip.svg') center no-repeat;
                background-size: cover;
            }
        }
        .name{
            font-size:14px;
            color: #363C54;
        }
        .follow{
            position: absolute;
            right: 25px;
            width: 65px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            font-size: 14px;
            border-radius: 15px;
            background-color: #f7f7f8;
            color:#CDCED4;
            &.but{
                color: #00BCBC;
                background-color: #E6FAFA;
            }
        }
        .icon_employees{
            margin-left: 5px;
            width: 20px;
            height: 20px;
            background: url('../../assets/svg/icon_employees.svg') center no-repeat;
            background-size: 20px;
        }
    }
    .comments_text{
        color: #363C54;
        font-size: 16px;
    }
    .comments_photo{
        position: relative;
        left: 0;
        top: 0;
        margin-top:20px;
        display: flex;
        flex-direction: row;
        .photo_li{
           width: 32%;
           height: 1.05rem;
           display:flex;
           align-items:center; 
           justify-content:center;
           overflow: hidden;
           line-height: 1.6rem;
           border-radius: 2px;
           background-color: #F7F7F8;
           img{
            height:100%;
            width: 100%;
            object-fit: cover;
           }
            img[lazy=loading] {
                width: 40px;
                height: 40px;
            }   
        }
        .photo_num{
            position: absolute;
            bottom: 10px;
            right: 10px;
            color: #FFFFFF;
            font-size: 34px;
            text-shadow:0px 0px 6px rgba(4, 11, 41, 0.3);
        }
    }
    .comments_information{
        margin-top: 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .time{
            font-size: 14px;
            color: #9B9DA9;
        }
        .comments_information_right{
            display: flex;
            flex-direction: row;
            color: #9B9DA9;
            font-size: 14px;
            .review{
                height: 20px;
                line-height: 20px;
                padding-left: 22px;
                background: url('../../assets/svg/icon_review.svg') left -2px no-repeat;
                background-size: 20px;
            }
            .praise{
                margin-left: 15.6px;
                height: 20px;
                line-height: 20px;
                padding-left: 22px;
                background: url('../../assets/svg/icon_praise.svg') left -1px no-repeat;
                background-size: 20px;
                &.select{
                    background: url('../../assets/svg/icon_praise_select.svg') left -1px no-repeat;
                    background-size: 20px;
                }
            }
        }
    }
</style>



